import { Percent } from '@iccswap/sdk'
import React, { useContext } from 'react'
import { ONE_BIPS } from '../../constants'
import { warningSeverity } from '../../utils/prices'
import { ErrorText } from './styleds'
import { ThemeContext } from 'styled-components'
import { Color } from '../../theme/styled'

/**
 * Formatted version of price impact text with warning colors
 */
export default function FormattedPriceImpact({ priceImpact, color }: { priceImpact?: Percent; color?: Color }) {
  const theme = useContext(ThemeContext)
  return (
    <ErrorText fontWeight={500} fontSize={14} severity={warningSeverity(priceImpact)} color={color ?? theme.text1}>
      {priceImpact ? (priceImpact.lessThan(ONE_BIPS) ? '<0.01%' : `${priceImpact.toFixed(2)}%`) : '-'}
    </ErrorText>
  )
}
