import React, { useCallback, useState, useEffect } from 'react'
import AppBody from '../AppBody'
import { ChangeIconWrapper, BottomGrouping, Wrapper } from '../../components/swap/styleds'
import { AutoColumn } from '../../components/Column'
import AppFooter from '../AppFooter'
import { Field } from '../../state/swap/actions'
import { AutoRow } from '../../components/Row'
import {
  useDefaultsFromURLSearch,
  useDerivedSwapInfo,
  useSwapActionHandlers,
  useSwapDiDiGasInfo,
  useSwapState
} from '../../state/swap/hooks'
import { useWrapCallbackDiDi, WrapType } from '../../hooks/useWrapCallback'
import { Version } from '../../hooks/useToggledVersion'
import { CurrencyAmount, JSBI, Trade } from '@iccswap/sdk'
import { maxAmountSpend } from '../../utils/maxAmountSpend'
import { useTranslation } from 'react-i18next'
import { ButtonError } from '../../components/Button'
import { GreyCard } from '../../components/Card'
import { ChangeIcon, TYPE } from '../../theme'
import { Text } from 'rebass'
import { useActiveWeb3React } from '../../hooks'
import { useDiDiGas, useSwapCallback } from '../../hooks/useSwapCallbackDiDi'
import { computeTradePriceBreakdown, warningSeverity } from '../../utils/prices'
import { useUserSlippageTolerance } from '../../state/user/hooks'
import ConfirmSwapModal from '../../components/swap/ConfirmSwapModal'
import AdvancedSwapDetailsDropdown from 'components/swap/AdvancedSwapDetailsDropdown'
import Header from 'components/Header'
import ChangeSvg from '../../assets/svg/change.svg'
import { CurrencyInputPanelDiDi } from '../../components/CurrencyInputPanel'
import TipModal from '../../components/TipModal'
import i18next from 'i18next'

export default function Swap() {
  // å¤šè¯­è¨€
  const { t } = useTranslation()
  // åŠ è½½é»˜è®¤
  useDefaultsFromURLSearch()
  // èŽ·å–ç”¨æˆ·ä¿¡æ¯
  const { account } = useActiveWeb3React()
  // èŽ·å–ç”¨æˆ·è®¾ç½®çš„ä»·æ ¼æ»‘ç‚¹
  const [allowedSlippage] = useUserSlippageTolerance()
  // swap state
  const { independentField, typedValue, recipient } = useSwapState()

  // eslint-disable-next-line prefer-const
  let { v2Trade, currencyBalances, parsedAmount, currencies, inputError: swapInputError } = useDerivedSwapInfo()
  const { wrapType } = useWrapCallbackDiDi(currencies[Field.INPUT], currencies[Field.OUTPUT], typedValue)
  const showWrap: boolean = wrapType !== WrapType.NOT_APPLICABLE
  const toggledVersion = Version.v2
  const tradesByVersion = {
    [Version.v2]: v2Trade
  }
  const trade = showWrap ? undefined : tradesByVersion[toggledVersion]

  const parsedAmounts = showWrap
    ? {
        [Field.INPUT]: parsedAmount,
        [Field.OUTPUT]: parsedAmount
      }
    : {
        [Field.INPUT]: independentField === Field.INPUT ? parsedAmount : trade?.inputAmount,
        [Field.OUTPUT]: independentField === Field.OUTPUT ? parsedAmount : trade?.outputAmount
      }

  const { onSwitchTokens, onCurrencySelection, onUserInput } = useSwapActionHandlers()
  const dependentField: Field = independentField === Field.INPUT ? Field.OUTPUT : Field.INPUT

  const handleTypeInput = useCallback(
    (value: string) => {
      onUserInput(Field.INPUT, value)
    },
    [onUserInput]
  )
  const handleTypeOutput = useCallback(
    (value: string) => {
      onUserInput(Field.OUTPUT, value)
    },
    [onUserInput]
  )

  const formattedAmounts = {
    [independentField]: typedValue,
    [dependentField]: showWrap
      ? parsedAmounts[independentField]?.toExact() ?? ''
      : parsedAmounts[dependentField]?.toSignificant(6) ?? ''
  }

  const maxAmountInput: CurrencyAmount | undefined = maxAmountSpend(currencyBalances[Field.INPUT])
  const atMaxAmountInput = Boolean(maxAmountInput && parsedAmounts[Field.INPUT]?.equalTo(maxAmountInput))

  const handleInputSelect = useCallback(
    inputCurrency => {
      handleTypeInput('')
      onCurrencySelection(Field.INPUT, inputCurrency)
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [onCurrencySelection]
  )

  const handleMaxInput = useCallback(() => {
    maxAmountInput && onUserInput(Field.INPUT, maxAmountInput.toExact())
  }, [maxAmountInput, onUserInput])
  const handleOutputSelect = useCallback(
    outputCurrency => {
      handleTypeOutput('')
      return onCurrencySelection(Field.OUTPUT, outputCurrency)
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [onCurrencySelection]
  )

  // modal and loading
  const [{ showConfirm, tradeToConfirm, swapErrorMessage, attemptingTxn, txHash }, setSwapState] = useState<{
    showConfirm: boolean
    tradeToConfirm: Trade | undefined
    attemptingTxn: boolean
    swapErrorMessage: string | undefined
    txHash: string | undefined
  }>({
    showConfirm: false,
    tradeToConfirm: undefined,
    attemptingTxn: false,
    swapErrorMessage: undefined,
    txHash: undefined
  })

  const route = trade?.route
  const userHasSpecifiedInputOutput = Boolean(
    currencies[Field.INPUT] && currencies[Field.OUTPUT] && parsedAmounts[independentField]?.greaterThan(JSBI.BigInt(0))
  )
  const noRoute = !route
  // the callback to execute the swap
  const { callback: swapCallback, error: swapCallbackError, swapCalls: swapSwapCalls } = useSwapCallback(
    trade,
    allowedSlippage,
    recipient
  )

  const { priceImpactWithoutFee } = computeTradePriceBreakdown(trade)

  const [showTipModal, setShowTipModal] = useState(false)
  // // æ£€æŸ¥ç”¨æˆ·ETH ä½™é¢
  const { error, txFee } = useDiDiGas(v2Trade, swapSwapCalls, allowedSlippage, swapInputError) || {
    error: undefined,
    txFee: undefined
  }
  useEffect(() => {
    if (error === 'è¯·ç¡®è®¤æ˜¯å¦éœ€è¦è‡ªåŠ¨ä¸Šå¸') {
      setShowTipModal(true)
    }
  }, [error])

  const { gasError } = useSwapDiDiGasInfo({
    v2Trade: trade,
    currencyBalances: currencyBalances,
    txFee: txFee
  })
  if (!swapInputError && (gasError || error)) {
    swapInputError = gasError || error
  }
  const isValid = !swapInputError
  const handleSwap = useCallback(() => {
    if (!swapCallback || !txFee) {
      return
    }
    setSwapState({ attemptingTxn: true, tradeToConfirm, showConfirm, swapErrorMessage: undefined, txHash: undefined })
    swapCallback(txFee)
      .then(hash => {
        setSwapState({ attemptingTxn: false, tradeToConfirm, showConfirm, swapErrorMessage: undefined, txHash: hash })
      })
      .catch(error => {
        // ç”¨æˆ·å–æ¶ˆä¸»åŠ¨å…³é—­ï¼Œä¸æ˜¾ç¤ºæé†’ï¼Œ2020-12-17
        if (error?.message === i18next.t('cancel')) {
          setSwapState({ showConfirm: false, tradeToConfirm, attemptingTxn, swapErrorMessage, txHash })
          // if there was a tx hash, we want to clear the input
          if (txHash) {
            onUserInput(Field.INPUT, '')
          }
        } else {
          setSwapState({
            attemptingTxn: false,
            tradeToConfirm,
            showConfirm,
            swapErrorMessage: error.message,
            txHash: undefined
          })
        }
      })
  }, [swapCallback, txFee, tradeToConfirm, showConfirm, attemptingTxn, swapErrorMessage, txHash, onUserInput])

  // warnings on slippage æ»‘ç‚¹è­¦å‘Š
  const priceImpactSeverity = warningSeverity(priceImpactWithoutFee)
  const handleConfirmDismiss = useCallback(() => {
    setSwapState({ showConfirm: false, tradeToConfirm, attemptingTxn, swapErrorMessage, txHash })
    // if there was a tx hash, we want to clear the input
    if (txHash) {
      onUserInput(Field.INPUT, '')
    }
  }, [attemptingTxn, onUserInput, swapErrorMessage, tradeToConfirm, txHash])

  const handleAcceptChanges = useCallback(() => {
    setSwapState({ tradeToConfirm: trade, swapErrorMessage, txHash, attemptingTxn, showConfirm })
  }, [attemptingTxn, showConfirm, swapErrorMessage, trade, txHash])

  return (
    <>
      <Header
        back={() => {
          const query = new URLSearchParams(window.location.href.substring(window.location.href.indexOf('?')))
          const url = query.get('url')
          if (url !== null) {
            window.location.href = url
          } else {
            window.location.href = process.env.REACT_APP_INFOHOST as string
          }
          // window.JSBridge.invoke('goBack', {})
        }}
        right="more"
      />
      {/*æ”¾å¼ƒä½¿ç”¨tokenè­¦å‘Š TokenWarningModal*/}
      <AppBody>
        <Wrapper id="swap-page">
          {/*è‡ªåŠ¨ä¸Šå¸å¼¹æ¡†*/}
          <TipModal
            isOpen={showTipModal}
            onDismiss={() => setShowTipModal(false)}
            currency={currencies[Field.OUTPUT]}
          />
          {/*ç¡®è®¤ä¿¡æ¯å¼¹çª—*/}
          <ConfirmSwapModal
            isOpen={showConfirm}
            trade={trade}
            originalTrade={tradeToConfirm}
            onAcceptChanges={handleAcceptChanges}
            attemptingTxn={attemptingTxn}
            txHash={txHash}
            recipient={recipient}
            allowedSlippage={allowedSlippage}
            onConfirm={handleSwap}
            swapErrorMessage={swapErrorMessage}
            onDismiss={handleConfirmDismiss}
            txFee={txFee}
          />
          <AutoColumn gap={'md'}>
            {/*è¾“å…¥ä»£å¸æ¡†*/}
            <CurrencyInputPanelDiDi
              label={independentField === Field.OUTPUT && !showWrap && trade ? 'From (estimated)' : 'From'}
              value={formattedAmounts[Field.INPUT]}
              showMaxButton={!atMaxAmountInput}
              currency={currencies[Field.INPUT]}
              onUserInput={handleTypeInput}
              onMax={handleMaxInput}
              onCurrencySelect={handleInputSelect}
              otherCurrency={currencies[Field.OUTPUT]}
              id="swap-currency-input"
              labelName={
                independentField === Field.OUTPUT && !showWrap && trade ? `${t('from')} (${t('estimated')})` : t('from')
              }
              showCommonBases={false}
            />
            {/*ä¸­é—´ç©ºæ ¼ï¼Œåˆ‡æ¢æ–¹å‘*/}
            <AutoColumn justify="space-between">
              <AutoRow justify={'center'} style={{ padding: '0 1rem' }}>
                <ChangeIconWrapper clickable>
                  <ChangeIcon
                    src={ChangeSvg}
                    alt="change"
                    size="36px"
                    onClick={() => {
                      onSwitchTokens()
                    }}
                  />
                </ChangeIconWrapper>
              </AutoRow>
            </AutoColumn>
            <CurrencyInputPanelDiDi
              value={formattedAmounts[Field.OUTPUT]}
              onUserInput={handleTypeOutput}
              label={independentField === Field.INPUT && !showWrap && trade ? 'To (estimated)' : 'To'}
              showMaxButton={false}
              currency={currencies[Field.OUTPUT]}
              onCurrencySelect={handleOutputSelect}
              otherCurrency={currencies[Field.INPUT]}
              id="swap-currency-output"
              labelName={
                independentField === Field.INPUT && !showWrap && trade ? `${t('to')} (${t('estimated')})` : t('to')
              }
              showCommonBases={false}
            />
          </AutoColumn>
          {/*å…‘æ¢è¯¦æƒ…*/}
          <AdvancedSwapDetailsDropdown trade={trade} txFee={txFee} />
          {/* ç¡®è®¤é€»è¾‘*/}
          <BottomGrouping>
            {!account ? (
              // åœ¨useSwapCallback ä¸­åŠ å…¥äº†é»˜è®¤ account
              'éœ€è¦è¿žæŽ¥é’±åŒ…'
            ) : noRoute && userHasSpecifiedInputOutput ? (
              <GreyCard style={{ textAlign: 'center' }}>
                <TYPE.main mb="4px">{t('Insufficient liquidity for this trade.')}</TYPE.main>
              </GreyCard>
            ) : (
              <ButtonError
                onClick={() => {
                  setSwapState({
                    tradeToConfirm: trade,
                    attemptingTxn: false,
                    swapErrorMessage: undefined,
                    showConfirm: true,
                    txHash: undefined
                  })
                }}
                id="swap-button"
                disabled={!isValid || priceImpactSeverity > 3 || !!swapCallbackError}
                error={isValid && priceImpactSeverity > 2 && !swapCallbackError}
              >
                <Text fontSize={16} fontWeight={500}>
                  {swapInputError
                    ? swapInputError
                    : priceImpactSeverity > 3
                    ? t(`Price Impact High`)
                    : `${priceImpactSeverity > 2 ? t('Swap anyway') : t('swap')}`}
                </Text>
              </ButtonError>
            )}
          </BottomGrouping>
        </Wrapper>
      </AppBody>
      <AppFooter />
    </>
  )
}
