import { Currency, ETHER, JSBI, TokenAmount } from '@iccswap/sdk'
import React, { useCallback, useEffect, useState, useContext } from 'react'
import Lock from '../../assets/images/lock.png'
import { useTranslation } from 'react-i18next'
import { Text } from 'rebass'
import { ButtonDropdownLight } from '../../components/Button'
import { LightCard } from '../../components/Card'
import { AutoColumn, ColumnCenter } from '../../components/Column'
import CurrencyLogo from '../../components/CurrencyLogo'
import { FindPoolTabs } from '../../components/NavigationTabs'
import { MinimalPositionCard } from '../../components/PositionCard'
import Row from '../../components/Row'
import CurrencySearchModal from '../../components/SearchModal/CurrencySearchModal'
import { PairState, usePair } from '../../data/Reserves'
import { useActiveWeb3React } from '../../hooks'
import { usePairAdder } from '../../state/user/hooks'
import { useTokenBalance } from '../../state/wallet/hooks'
import { StyledInternalLink } from '../../theme'
import { currencyId } from '../../utils/currencyId'
import AppBody from '../AppBody'
import { Dots } from '../Pool/styleds'
import { ThemeContext } from 'styled-components'
import FooterWrapper from '../AppFooter'
import styled from 'styled-components'
import Header from 'components/Header'

const FlexAuto = styled.div`
  width: 100%;
  height: 85vh;
  display: flex;
  flex-direction: column;
  margin-top: 40px;
`

enum Fields {
  TOKEN0 = 0,
  TOKEN1 = 1
}

export default function PoolFinder() {
  const theme = useContext(ThemeContext)
  const { account } = useActiveWeb3React()
  const { t } = useTranslation()

  const [showSearch, setShowSearch] = useState<boolean>(false)
  const [activeField, setActiveField] = useState<number>(Fields.TOKEN1)

  const [currency0, setCurrency0] = useState<Currency | null>(ETHER)
  const [currency1, setCurrency1] = useState<Currency | null>(null)

  const [pairState, pair] = usePair(currency0 ?? undefined, currency1 ?? undefined)
  const addPair = usePairAdder()
  useEffect(() => {
    if (pair) {
      addPair(pair)
    }
  }, [pair, addPair])

  const validPairNoLiquidity: boolean =
    pairState === PairState.NOT_EXISTS ||
    Boolean(
      pairState === PairState.EXISTS &&
        pair &&
        JSBI.equal(pair.reserve0.raw, JSBI.BigInt(0)) &&
        JSBI.equal(pair.reserve1.raw, JSBI.BigInt(0))
    )

  const position: TokenAmount | undefined = useTokenBalance(account ?? undefined, pair?.liquidityToken)
  const hasPosition = Boolean(position && JSBI.greaterThan(position.raw, JSBI.BigInt(0)))

  const handleCurrencySelect = useCallback(
    (currency: Currency) => {
      if (activeField === Fields.TOKEN0) {
        setCurrency0(currency)
      } else {
        setCurrency1(currency)
      }
    },
    [activeField]
  )

  const handleSearchDismiss = useCallback(() => {
    setShowSearch(false)
  }, [setShowSearch])

  const prerequisiteMessage = (
    <LightCard padding="45px 10px" style={{ background: `${theme.bg2}` }}>
      <Text textAlign="center" fontSize={14}>
        {!account ? 'Connect to a wallet to find pools' : `${t('Select a token to find your liquidity')}`}
      </Text>
    </LightCard>
  )

  return (
    <>
      <Header title={t('ImportPool')} right={t('ImportQuestion')} />
      <AppBody>
        <FlexAuto>
          <FindPoolTabs />
          <AutoColumn gap="md">
            <ButtonDropdownLight
              onClick={() => {
                setShowSearch(true)
                setActiveField(Fields.TOKEN0)
              }}
            >
              {currency0 ? (
                <Row>
                  <CurrencyLogo currency={currency0} />
                  <Text color={theme.text1} fontWeight={500} fontSize={20} marginLeft={'12px'}>
                    {currency0.symbol}
                  </Text>
                </Row>
              ) : (
                <Text color={theme.text1} fontWeight={500} fontSize={20} marginLeft={'12px'}>
                  {t('Select a Token')}
                </Text>
              )}
            </ButtonDropdownLight>

            <ColumnCenter>
              <div
                style={{
                  background: `${theme.bg4}`,
                  width: '32px',
                  height: '32px',
                  borderRadius: '50%',
                  display: 'flex',
                  justifyContent: 'center',
                  alignItems: 'center'
                }}
              >
                <img width={18} src={Lock} alt="" />
              </div>
            </ColumnCenter>

            <ButtonDropdownLight
              onClick={() => {
                setShowSearch(true)
                setActiveField(Fields.TOKEN1)
              }}
            >
              {currency1 ? (
                <Row>
                  <CurrencyLogo currency={currency1} />
                  <Text fontWeight={500} fontSize={20} marginLeft={'12px'}>
                    {currency1.symbol}
                  </Text>
                </Row>
              ) : (
                <Text fontWeight={500} fontSize={20} marginLeft={'12px'}>
                  {t('Select a Token')}
                </Text>
              )}
            </ButtonDropdownLight>

            {hasPosition && (
              <ColumnCenter
                style={{
                  flexDirection: 'row',
                  justifyContent: 'center',
                  backgroundColor: '',
                  padding: '12px 0px',
                  borderRadius: '12px',
                  display: 'flex'
                }}
              >
                <Text textAlign="center" fontWeight={500} fontSize={14}>
                  {t('Pool Found')}&nbsp;
                </Text>
                <StyledInternalLink to={`/pool`}>
                  <Text textAlign="center" fontSize={14}>
                    {t('Manage this pool')}.
                  </Text>
                </StyledInternalLink>
              </ColumnCenter>
            )}

            {currency0 && currency1 ? (
              pairState === PairState.EXISTS ? (
                hasPosition && pair ? (
                  <MinimalPositionCard pair={pair} border="1px solid #CED0D9" />
                ) : (
                  <LightCard padding="45px 10px" style={{ background: `${theme.bg2}` }}>
                    <AutoColumn gap="sm" justify="center" style={{ display: 'flex', justifyContent: 'center' }}>
                      <Text textAlign="center" fontSize={14}>
                        {t('You donâ€™t have liquidity in this pool yet')}&nbsp;
                      </Text>
                      <StyledInternalLink to={`/add/${currencyId(currency0)}/${currencyId(currency1)}`}>
                        <Text textAlign="center" fontSize={14}>
                          {t('addLiquidity')}
                        </Text>
                      </StyledInternalLink>
                    </AutoColumn>
                  </LightCard>
                )
              ) : validPairNoLiquidity ? (
                <LightCard padding="45px 10px" style={{ background: `${theme.bg2}` }}>
                  <AutoColumn gap="sm" justify="center" style={{ display: 'flex', justifyContent: 'center' }}>
                    <Text textAlign="center" fontSize={14}>
                      {t('No pool found')}{' '}
                    </Text>
                    <StyledInternalLink
                      to={`/add/${currencyId(currency0)}/${currencyId(currency1)}`}
                      style={{ fontSize: '14px' }}
                    >
                      {t('Create pool')}
                    </StyledInternalLink>
                  </AutoColumn>
                </LightCard>
              ) : pairState === PairState.INVALID ? (
                <LightCard padding="45px 10px" style={{ background: `${theme.bg2}` }}>
                  <AutoColumn gap="sm" justify="center" style={{ display: 'flex', justifyContent: 'center' }}>
                    <Text textAlign="center" fontWeight={500} fontSize={14}>
                      {t('Invalid pair')}
                    </Text>
                  </AutoColumn>
                </LightCard>
              ) : pairState === PairState.LOADING ? (
                <LightCard padding="45px 10px" style={{ background: `${theme.bg2}` }}>
                  <AutoColumn gap="sm" justify="center" style={{ display: 'flex', justifyContent: 'center' }}>
                    <Text textAlign="center" fontSize={14}>
                      {t('Loading')}
                      <Dots />
                    </Text>
                  </AutoColumn>
                </LightCard>
              ) : null
            ) : (
              prerequisiteMessage
            )}
          </AutoColumn>
          <FooterWrapper></FooterWrapper>
        </FlexAuto>

        <CurrencySearchModal
          isOpen={showSearch}
          onCurrencySelect={handleCurrencySelect}
          onDismiss={handleSearchDismiss}
          showCommonBases
          selectedCurrency={(activeField === Fields.TOKEN0 ? currency1 : currency0) ?? undefined}
        />
      </AppBody>
    </>
  )
}
