import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import AppFooter from '../AppFooter'
import Vertical from '../../assets/images/vertical.png'
import Right from '../../assets/images/arrow-tail-down.png'
import Failed from '../../assets/images/failed.png'
import Success from '../../assets/images/success.png'
import Add from '../../assets/images/lock.png'
import Countdown from '../../assets/images/countdown.png'
import { RowCenter, RowBetween } from 'components/Row'
import Row from 'components/Row'
import axios from '../../axios'
import Header from 'components/Header'
import { useTranslation } from 'react-i18next'
import { Text } from 'rebass'
import { formatTimeStamp } from 'utils'
import QuestionHelper from '../../components/QuestionHelper'

export default function Record() {
  const { t } = useTranslation()
  const Container = styled.div`
    width: 100%;
    margin-bottom: 30px;
    border-top: 1px solid ${({ theme }) => theme.bg8};
  `
  const Title = styled.div`
    width: 100%;
    height: 50px;
    background: ${({ theme }) => theme.bg1};
    display: flex;
    justify-content: center;
    align-items: center;
    color: #999999;
  `
  const TitleGray = styled.div`
    margin: 0 20px;
    padding-bottom: 3px;
  `
  const TitleActive = styled.div`
    color: rgba(47, 84, 235, 1);
    margin: 0 20px;
    padding-bottom: 3px;
    border-bottom: 2px solid #2f54eb;
    border-radius: 2px;
  `
  const Record = styled.div`
    padding: 15px;
    width: 100%;
  `
  const Info = styled.div`
    width: 100%;
    height: 180px;
    background: ${({ theme }) => theme.bg13};
    border-radius: 4px;
    margin-top: 6px;
    box-sizing: border-box;
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    border: 1px solid rgba(88, 127, 248, 0.22);
  `
  const InfoGreen = styled.div`
    color: #21a313;
    display: flex;
    align-items: center;
  `
  const InfoTime = styled.div`
    color: ${({ theme }) => theme.text6};
    display: flex;
    align-items: center;
  `
  const InfoRed = styled.div`
    color: #d53432;
    display: flex;
    align-items: center;
  `
  const InfoOrange = styled.div`
    color: #ff704c;
    display: flex;
    align-items: center;
  `
  const FeeBox = styled.div`
    color: #8a8e99;
  `
  const Line = styled.div`
    border-top: 1px solid ${({ theme }) => theme.line};
    width: 90%;
  `
  const TitleSwap = styled.div`
    color: ${({ theme }) => theme.text1};
    font-size: 15px;
    font-weight: 550;
  `
  const TitleOne = styled.div`
    width: 100%;
    background: ${({ theme }) => theme.bg12};
    font-size: 15px;
    font-weight: 550;
    display: flex;
    justify-content: space-between;
  `
  type Status = '0' | '1' | '2'
  type SwapType = '1' | '2' | '3'
  const statusType = {
    '0': {
      name: `${t('processing')}`,
      icon: Countdown
    },
    '1': {
      name: `${t('successful')}`,
      icon: Success
    },
    '2': {
      name: `${t('failed')}`,
      icon: Failed
    }
  }
  const swapTypeName = {
    '1': `${t('swap')}`,
    '2': `${t('Add')}`,
    '3': `${t('withdraw')}`
  }
  interface SwapOtcRecord {
    actualAmounta: string
    actualAmountb: string
    actualFee: string
    createdOn: string
    currencyNamea: string
    currencyNameb: string
    currencya: string
    currencyb: string
    serviceFee: string
    status: Status
    swapType: SwapType
    symbola: string
    symbolb: string
    userId: number
  }
  const [list, setList] = useState<SwapOtcRecord[]>([])
  const [pageNo, setPageNo] = useState(1)
  const [total, setTotal] = useState(0)
  const tabs = [`${t('All')}`, `${t('swaps')}`, `${t('Add')}`, `${t('withdraw')}`]
  const [curTab, setcurTab] = useState(0)
  const urlAddress = process.env.REACT_APP_HOST + '/getImageByCurrency?currency='
  // useEffect(() => {
  //   ;(async () => {
  //     const res = await axios({
  //       url: '/getSwapOtcRecord',
  //       method: 'post',
  //       data: {
  //         pageNo: pageNo,
  //         pageSize: 20,
  //         swapType: curTab === 0 ? undefined : curTab.toString() //ç±»åž‹ 1:å‘èµ·å…‘æ¢,2:æ·»åŠ æµåŠ¨æ€§,3:ç§»é™¤æµåŠ¨æ€§
  //       }
  //     })
  //     setTotal(res.data.totalRow)
  //     setList(list ? [...list, ...res.data.rows] : res.data.rows)
  //   })()
  //   // eslint-disable-next-line react-hooks/exhaustive-deps
  // }, [curTab, pageNo])
  useEffect(() => {
    axios({
      url: '/getSwapOtcRecord',
      method: 'post',
      data: {
        pageNo: pageNo,
        pageSize: 20,
        swapType: curTab === 0 ? undefined : curTab.toString() //ç±»åž‹ 1:å‘èµ·å…‘æ¢,2:æ·»åŠ æµåŠ¨æ€§,3:ç§»é™¤æµåŠ¨æ€§
      }
    }).then((res: any) => {
      setTotal(res.data.totalRow)
      setList(list ? [...list, ...res.data.rows] : res.data.rows)
      if (res.code !== '200') {
        console.log(123)
      }
    })
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [curTab, pageNo])

  return (
    <>
      <Header title={t('record')} />
      <Container>
        <Title>
          {tabs.map((item, index) => {
            return (
              <div key={item}>
                {curTab !== index ? (
                  <TitleGray
                    onClick={() => {
                      setTotal(0)
                      setList([])
                      setPageNo(1)
                      setcurTab(index)
                    }}
                  >
                    {item}
                  </TitleGray>
                ) : (
                  <TitleActive>{item}</TitleActive>
                )}
                {index === tabs.length - 1 ?? <img src={Vertical} alt="" />}
              </div>
            )
          })}
        </Title>

        <Record>
          {list.map((item, index) => {
            return (
              <Info key={index}>
                <TitleOne style={{ height: '45px', padding: '0 15px' }}>
                  <InfoTime>
                    <TitleSwap>{swapTypeName[item.swapType]}</TitleSwap>
                    &nbsp;&nbsp; {formatTimeStamp(item.createdOn)}
                  </InfoTime>
                  <RowCenter>
                    {item.status === '1' && (
                      <InfoGreen>
                        {statusType[item.status].name}
                        <img style={{ width: '20px', marginLeft: '5px' }} src={statusType[item.status].icon} alt="" />
                      </InfoGreen>
                    )}
                    {item.status === '2' && (
                      <InfoRed>
                        {statusType[item.status].name}
                        <img style={{ width: '18px', marginLeft: '5px' }} src={statusType[item.status].icon} alt="" />
                      </InfoRed>
                    )}
                    {item.status === '0' && (
                      <InfoOrange>
                        {statusType[item.status].name}
                        <img style={{ width: '18px', marginLeft: '5px' }} src={statusType[item.status].icon} alt="" />
                      </InfoOrange>
                    )}
                  </RowCenter>
                </TitleOne>
                <RowBetween
                  style={{
                    padding: '0 15px',
                    height: '76px',
                    display: 'flex',
                    flexDirection: 'column',
                    marginTop: '10px'
                  }}
                >
                  <Row>
                    <img style={{ width: '20px', height: '20px' }} src={urlAddress + `${item.currencya}`} alt="" />
                    <RowCenter>
                      <Text style={{ marginLeft: '10px' }}>
                        {item.actualAmounta}&nbsp;
                        {item.symbola}
                      </Text>
                    </RowCenter>
                  </Row>
                  <Row>
                    {item.swapType === '2' ? (
                      <img style={{ width: '15px', margin: '0 5px' }} src={Add} alt="" />
                    ) : (
                      <img style={{ width: '10px', margin: '0 5px', height: '16px' }} src={Right} alt="" />
                    )}
                    <Line></Line>
                  </Row>
                  <Row>
                    <img style={{ width: '20px', height: '20px' }} src={urlAddress + `${item.currencyb}`} alt="" />
                    <RowCenter>
                      <Text style={{ marginLeft: '10px' }}>
                        {item.actualAmountb}&nbsp;
                        {item.symbolb}
                      </Text>
                    </RowCenter>
                  </Row>
                </RowBetween>
                <RowBetween style={{ padding: '10px 15px' }}>
                  <FeeBox style={{ display: 'flex', alignItems: 'center', fontSize: '12px' }}>
                    {item.status === '0' ? `${t('Yfee')}` : `${t('Kfee')}`}
                    <QuestionHelper text={t('fee')} />
                  </FeeBox>
                  <FeeBox>{item.serviceFee} ETH</FeeBox>
                </RowBetween>
              </Info>
            )
          })}
        </Record>
        {list.length < total ? (
          <RowCenter style={{ marginBottom: '20px' }}>
            <span onClick={() => setPageNo(pageNo + 1)}>{t('more')}</span>
          </RowCenter>
        ) : null}
      </Container>
      <AppFooter />
    </>
  )
}
