/*åˆ¤æ–­å®¢æˆ·ç«¯*/
export function judgeClient() {
  const ua = navigator.userAgent.toLowerCase()

  if (ua === 'android') {
    return 'android'
  } else if (ua === 'ios') {
    return 'ios'
  } else {
    return 'pc'
  }
}

export default function() {
  const curClient = judgeClient()
  let id = 0,
    callbacks = {},
    registerFuncs = {}

  window.JSBridge = {
    // è°ƒç”¨ Native
    invoke: function(bridgeName = '', data = {}, callback = () => {}) {
      if (curClient === 'pc') return
      const thisId = id++ // èŽ·å–å”¯ä¸€ id
      callbacks[thisId] = callback // å­˜å‚¨ Callback
      switch (curClient) {
        case 'ios':
          window.webkit.messageHandlers.nativeBridge.postMessage({
            bridgeName: bridgeName,
            data: data || {},
            callbackId: thisId // ä¼ åˆ° Native ç«¯
          })
          return
        case 'android':
          return window.nativeBridge.postMessage(
            JSON.stringify({
              bridgeName: bridgeName,
              data: data || {},
              callbackId: thisId // ä¼ åˆ° Native ç«¯
            })
          )
        // break;
        default:
          //pc
          return
      }
    },
    receiveMessage: function(msg, error) {
      let msg3
      if (typeof msg == 'string') {
        msg3 = JSON.parse(msg)
      } else {
        msg3 = msg
      }

      const { bridgeName, data, callbackId, responstId } = msg3
      // å…·ä½“é€»è¾‘
      // bridgeName å’Œ callbackId ä¸ä¼šåŒæ—¶å­˜åœ¨
      if (typeof callbackId === 'number') {
        if (callbacks[callbackId]) {
          // æ‰¾åˆ°ç›¸åº”å¥æŸ„
          callbacks[callbackId](data, error) // æ‰§è¡Œè°ƒç”¨
        }
      } else if (bridgeName) {
        if (registerFuncs[bridgeName]) {
          // é€šè¿‡ bridgeName æ‰¾åˆ°å¥æŸ„
          let ret = {},
            flag = false
          registerFuncs[bridgeName].forEach(function(callback) {
            callback(data, function(r) {
              flag = true
              ret = Object.assign(ret, r)
            })
          })
          if (flag) {
            window.nativeBridge.postMessage({
              // å›žè°ƒ Native
              responstId: responstId,
              ret: ret
            })
          }
        }
      }
    },
    register: function(bridgeName, callback) {
      if (!registerFuncs[bridgeName]) {
        registerFuncs[bridgeName] = []
      }
      registerFuncs[bridgeName].push(callback) // å­˜å‚¨å›žè°ƒ
    }
  }
}
