import React, { useState } from 'react'
import { NavLink, useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import RecordModal from './RecordModal'

import styled from 'styled-components'

import Back from '../../assets/images/back.png'
import Dot from '../../assets/images/dot.png'
import Dot2 from '../../assets/images/dot2.png'
import Back2 from '../../assets/images/back2.png'

import Row, { RowFixed, RowBetween } from '../Row'
import { useIsDarkMode } from 'state/user/hooks'
import QuestionHelper from 'components/QuestionHelper'

const HeaderFrame = styled.div`
  display: grid;
  grid-template-columns: 1fr 120px;
  align-items: center;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  width: 100%;
  top: 0;
  position: relative;
  z-index: 2;
  ${({ theme }) => theme.mediaWidth.upToMedium`
    grid-template-columns: 1fr;
    width: calc(100%);
    position: relative;
    z-index: 1;
  `};
`

const HeaderRow = styled(RowFixed)`
  ${({ theme }) => theme.mediaWidth.upToMedium`
   width: 100%;
   background-color: ${({ theme }) => theme.bg1};
   height: 44px;
   padding: 0 15px;
  `};
`

const HeaderLinks = styled(Row)`
  justify-content: center;
  ${({ theme }) => theme.mediaWidth.upToMedium`
    justify-content: center;
`};
`

const HeaderRight = styled(RowBetween)`
  width: 30px;
  &img {
    margin-left: 20px;
  }
`

const activeClassName = 'ACTIVE'

const StyledNavLink = styled(NavLink).attrs({
  activeClassName
})`
  ${({ theme }) => theme.flexRowNoWrap}
  align-items: left;
  border-radius: 3rem;
  outline: none;
  cursor: pointer;
  text-decoration: none;
  color: ${({ theme }) => theme.text1};
  font-size: 1rem;
  width: fit-content;
  margin: 0 12px;
  font-weight: 500;

  &.${activeClassName} {
    border-radius: 12px;
    font-weight: 600;
    color: ${({ theme }) => theme.text2};
  }
`

interface Props {
  back?: () => void
  title?: string
  right?: string
}
export default function Header(props: Props) {
  const { title, right, back } = props
  const { t } = useTranslation()
  const darkMode = useIsDarkMode()
  const [showRecordModal, setShowRecordModal] = useState(false)
  const history = useHistory()
  return (
    <HeaderFrame>
      <HeaderRow>
        <img
          onClick={() => {
            typeof back !== 'undefined' ? back() : history.goBack()
          }}
          width={'22px'}
          src={darkMode ? Back : Back2}
          alt="back"
        />
        <HeaderLinks>
          {typeof title === 'undefined' ? (
            <>
              <StyledNavLink id={`swap-nav-link`} to={'/swap'}>
                {t('swap')}
              </StyledNavLink>
              <StyledNavLink
                id={`pool-nav-link`}
                to={'/pool'}
                isActive={(match, { pathname }) =>
                  Boolean(match) ||
                  pathname.startsWith('/add') ||
                  pathname.startsWith('/remove') ||
                  pathname.startsWith('/create') ||
                  pathname.startsWith('/find')
                }
              >
                {t('pool')}
              </StyledNavLink>
            </>
          ) : (
            <span>{title}</span>
          )}
        </HeaderLinks>
        {right === 'more' && (
          <HeaderRight>
            <img onClick={() => setShowRecordModal(true)} width={24} src={darkMode ? Dot2 : Dot} alt="" />
            <RecordModal isOpen={showRecordModal} onDismiss={() => setShowRecordModal(false)} />
          </HeaderRight>
        )}
        {typeof right === 'string' && right !== 'more' && <QuestionHelper text={right}></QuestionHelper>}
      </HeaderRow>
    </HeaderFrame>
  )
}
