import React from 'react'
import styled from 'styled-components'
// import { RowBetween, RowFlex, RowCenter } from '../../components/Row'
import FooterWrapper from '../AppFooter'
import LiquitidyPool from 'components/LiquitidyPool/index'
// import Down from '../../assets/images/down.png'
import Header from 'components/Header'

import { ButtonPrimary } from '../../components/Button'
import { Text } from 'rebass'
import { Link } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import { AutoColumn } from 'components/Column'
import { StyledInternalLink } from 'theme'

const Container = styled.div`
  width: 100%;
  padding: 0 15px;
  padding-top: 30px;
`
const ResponsiveButtonPrimary = styled(ButtonPrimary)`
  width: fit-content;
  width: 100%;
  background: #2f54eb;
  height: 50px;
`

export default function Pool() {
  const { t } = useTranslation()
  return (
    <>
      <Header
        back={() => {
          const query = new URLSearchParams(window.location.href.substring(window.location.href.indexOf('?')))
          const url = query.get('url')
          if (url !== null) {
            window.location.href = url
          } else {
            window.location.href = process.env.REACT_APP_INFOHOST as string
          }

          // window.JSBridge.invoke('goBack', {})
        }}
        right="more"
      />
      <Container>
        <ResponsiveButtonPrimary
          id="join-pool-button"
          as={Link}
          padding="6px 8px"
          to="/add/ETH/0x516de3a7A567d81737e3a46ec4FF9cFD1fcb0136"
        >
          <Text fontWeight={500} fontSize={16}>
            {t('add')}
          </Text>
        </ResponsiveButtonPrimary>
        <LiquitidyPool />
        <AutoColumn justify={'center'} gap="md" style={{ marginTop: '20px' }}>
          <Text textAlign="center" fontSize={14} style={{ padding: '.5rem 0 .5rem 0' }}>
            <StyledInternalLink id="import-pool-link" to={'/find'}>
              <span style={{ color: 'rgba(153, 153, 153, 100)' }}>{t('Joined')}&nbsp;</span>
              {t('Import')}
            </StyledInternalLink>
          </Text>
        </AutoColumn>
      </Container>
      <FooterWrapper></FooterWrapper>
    </>
  )
}
