import { Currency, Percent, Price } from '@iccswap/sdk'
import React, { useContext, useState } from 'react'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { AutoColumn } from '../../components/Column'
import { RowBetween, RowFlex } from '../../components/Row'
import { ONE_BIPS } from '../../constants'
import { Field } from '../../state/mint/actions'
import Switch from '../../assets/images/switch.png'
import { Txfee } from './index'
import { useTranslation } from 'react-i18next'

export function PoolPriceBar({
  currencies,
  noLiquidity,
  poolTokenPercentage,
  price,
  fee
}: {
  currencies: { [field in Field]?: Currency }
  noLiquidity?: boolean
  poolTokenPercentage?: Percent
  price?: Price
  fee?: Txfee
}) {
  const theme = useContext(ThemeContext)
  const [showState, setShowState] = useState<boolean>(true)
  const { t } = useTranslation()

  return (
    <AutoColumn gap="md" style={{ padding: '0 10px', marginBottom: '20px' }}>
      <RowBetween>
        <Text fontWeight={500} fontSize={14} color={theme.text1}>
          {t('Price')}ï¼š
        </Text>
        <RowFlex>
          {showState ? (
            <Text fontWeight={500} fontSize={14} color={theme.text1}>
              {price?.invert()?.toSignificant(6) ?? '-'} {currencies[Field.CURRENCY_A]?.symbol}/
              {currencies[Field.CURRENCY_B]?.symbol}
            </Text>
          ) : (
            <Text fontWeight={500} fontSize={14} color={theme.text1}>
              {price?.toSignificant(6) ?? '-'} {currencies[Field.CURRENCY_B]?.symbol}/
              {currencies[Field.CURRENCY_A]?.symbol}
            </Text>
          )}
          <img
            onClick={() => setShowState(!showState)}
            style={{ width: '16px', height: '16px', marginLeft: '10px' }}
            src={Switch}
            alt=""
          />
        </RowFlex>
      </RowBetween>
      <RowBetween>
        <Text fontWeight={500} fontSize={14} color={theme.text1}>
          {t('Share of Pool')}ï¼š
        </Text>
        <Text fontWeight={500} fontSize={14} color={theme.text1}>
          {noLiquidity && price
            ? '100'
            : (poolTokenPercentage?.lessThan(ONE_BIPS) ? '<0.01' : poolTokenPercentage?.toFixed(2)) ?? '0'}
          %
        </Text>
      </RowBetween>
      <RowBetween>
        <Text fontWeight={500} fontSize={14} color={theme.text1}>
          {t('fee')}ï¼š
        </Text>
        <Text fontWeight={500} fontSize={14} color={theme.text1}>
          {fee?.gasFee} ETH
        </Text>
      </RowBetween>
    </AutoColumn>
  )
}
