import { Trade, TradeType } from '@iccswap/sdk'
import React, { useContext, useMemo, useState } from 'react'
import { Repeat } from 'react-feather'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { Field } from '../../state/swap/actions'
import { TYPE } from '../../theme'
import {
  computeSlippageAdjustedAmounts,
  computeTradePriceBreakdown,
  formatExecutionPrice,
  warningSeverity
} from '../../utils/prices'
import { ButtonError, ButtonPrimary } from '../Button'
import { AutoColumn } from '../Column'
import QuestionHelper from '../QuestionHelper'
import { AutoRow, RowBetween, RowFixed } from '../Row'
import FormattedPriceImpact from './FormattedPriceImpact'
import { StyledBalanceMaxMini, SwapCallbackError, SwapShowAcceptChanges } from './styleds'
import { useTranslation } from 'react-i18next'
import { BigNumber } from 'bignumber.js'
import { DiDiTxFee } from '../../utils/didi'

export default function SwapModalFooter({
  trade,
  onConfirm,
  allowedSlippage,
  swapErrorMessage,
  disabledConfirm,
  onAcceptChanges,
  txFee: gasInfo
}: {
  trade: Trade
  allowedSlippage: number
  onConfirm: () => void
  swapErrorMessage: string | undefined
  disabledConfirm: boolean
  onAcceptChanges: () => void
  txFee: DiDiTxFee | undefined
}) {
  const { t } = useTranslation()
  const [showInverted, setShowInverted] = useState<boolean>(false)
  const theme = useContext(ThemeContext)
  const slippageAdjustedAmounts = useMemo(() => computeSlippageAdjustedAmounts(trade, allowedSlippage), [
    allowedSlippage,
    trade
  ])
  const { priceImpactWithoutFee, realizedLPFee } = useMemo(() => computeTradePriceBreakdown(trade), [trade])
  const severity = warningSeverity(priceImpactWithoutFee)
  return (
    <>
      {/*å…‘æ¢è¯¦æƒ…*/}
      <AutoColumn gap="0px" style={{ backgroundColor: `${theme.search}`, padding: '1rem', color: `${theme.text1}` }}>
        {/*ä»·æ ¼*/}
        <RowBetween align="center">
          <Text fontWeight={400} fontSize={14} color={theme.text1}>
            {t('Estimated price')}
          </Text>
          <Text
            fontWeight={500}
            fontSize={14}
            color={theme.text1}
            style={{
              justifyContent: 'center',
              alignItems: 'center',
              display: 'flex',
              textAlign: 'right',
              paddingLeft: '10px'
            }}
          >
            {formatExecutionPrice(trade, showInverted)}
            <StyledBalanceMaxMini onClick={() => setShowInverted(!showInverted)}>
              <Repeat size={14} />
            </StyledBalanceMaxMini>
          </Text>
        </RowBetween>
        {/*æœ€ä½Žæ”¶åˆ°/æœ€é«˜æ”¯å‡º*/}
        <RowBetween>
          <RowFixed>
            <TYPE.black fontSize={14} fontWeight={400} color={theme.text1}>
              {trade.tradeType === TradeType.EXACT_INPUT ? t('Minimum received') : t('Maximum sold')}
            </TYPE.black>
            <QuestionHelper
              text={t(
                'Your transaction will revert if there is a large, unfavorable price movement before it is confirmed.'
              )}
            />
          </RowFixed>
          <RowFixed>
            <TYPE.black fontSize={14} color={theme.text1}>
              {trade.tradeType === TradeType.EXACT_INPUT
                ? slippageAdjustedAmounts[Field.OUTPUT]?.toSignificant(4) ?? '-'
                : slippageAdjustedAmounts[Field.INPUT]?.toSignificant(4) ?? '-'}
            </TYPE.black>
            <TYPE.black fontSize={14} marginLeft={'4px'} color={theme.text1}>
              {trade.tradeType === TradeType.EXACT_INPUT
                ? trade.outputAmount.currency.symbol
                : trade.inputAmount.currency.symbol}
            </TYPE.black>
          </RowFixed>
        </RowBetween>
        {/*ä»·æ ¼å½±å“*/}
        <RowBetween>
          <RowFixed>
            <TYPE.black color={theme.text1} fontSize={14} fontWeight={400}>
              {t('Price Impact')}
            </TYPE.black>
            <QuestionHelper
              text={t('The difference between the market price and estimated price due to trade size.')}
            />
          </RowFixed>
          <FormattedPriceImpact priceImpact={priceImpactWithoutFee} />
        </RowBetween>
        {/*æ‰‹ç»­è´¹*/}
        <RowBetween>
          <RowFixed>
            <TYPE.black fontSize={14} fontWeight={400} color={theme.text1}>
              {t('Liquidity Provider Fee')}
            </TYPE.black>
          </RowFixed>
          <TYPE.black fontSize={14} color={theme.text1}>
            {realizedLPFee
              ? `${
                  gasInfo?.gasFee != null
                    ? new BigNumber(gasInfo.gasFee).plus(new BigNumber(gasInfo.serviceFee)).toFixed(18)
                    : '--'
                } ${'ETH'}`
              : '-'}
          </TYPE.black>
        </RowBetween>
      </AutoColumn>
      {/* ä»·æ ¼æ”¹å˜æé†’ */}
      {disabledConfirm ? (
        <SwapShowAcceptChanges justify="flex-start" gap={'0px'}>
          <RowBetween color={theme.white}>
            <RowFixed>
              <TYPE.main color={theme.white} fontWeight={400}>
                {t('Price Updated')}
              </TYPE.main>
            </RowFixed>
            <ButtonPrimary
              style={{
                backgroundColor: theme.white,
                color: '#FF704C',
                padding: '.5rem',
                width: 'fit-content',
                fontSize: '0.825rem',
                borderRadius: '30px'
              }}
              onClick={onAcceptChanges}
            >
              {t('Accept')}
            </ButtonPrimary>
          </RowBetween>
        </SwapShowAcceptChanges>
      ) : null}
      <AutoRow>
        {/* å…‘æ¢é¢„ä¼°æé†’ */}
        <AutoColumn justify="flex-start" gap="sm" style={{ padding: '12px 0 0 0px' }}>
          {trade.tradeType === TradeType.EXACT_INPUT ? (
            <TYPE.italic textAlign="left" style={{ width: '100%' }}>
              <span
                dangerouslySetInnerHTML={{
                  __html: t('Output is estimated. You will receive at least x or the transaction will revert.', {
                    amount: slippageAdjustedAmounts[Field.OUTPUT]?.toSignificant(6),
                    currency: trade.outputAmount.currency.symbol
                  })
                }}
              />
            </TYPE.italic>
          ) : (
            <TYPE.italic textAlign="left" style={{ width: '100%' }}>
              <span
                dangerouslySetInnerHTML={{
                  __html: t('Input is estimated. You will sell at most  xxx  or the transaction will revert.', {
                    amount: slippageAdjustedAmounts[Field.INPUT]?.toSignificant(6),
                    currency: trade.inputAmount.currency.symbol
                  })
                }}
              />
            </TYPE.italic>
          )}
        </AutoColumn>
        <ButtonError
          onClick={onConfirm}
          disabled={disabledConfirm}
          error={severity > 2}
          style={{ margin: '10px 0 0 0', padding: '12px' }}
          id="confirm-swap-or-send"
        >
          <Text fontSize={16} fontWeight={400}>
            {severity > 2 ? t('Swap anyway') : t('Confirm Swap')}
          </Text>
        </ButtonError>

        {swapErrorMessage ? <SwapCallbackError error={swapErrorMessage} /> : null}
      </AutoRow>
    </>
  )
}
