import { Contract } from '@ethersproject/contracts'
import { JSBI, Percent, Router, SwapParameters, Token, Trade, TradeType } from '@iccswap/sdk'
import { useEffect, useMemo, useState } from 'react'
import { BIPS_BASE, INITIAL_ALLOWED_SLIPPAGE } from '../constants'
import { basisPointsToPercent, getRouterContract, uuid } from '../utils'
import { useActiveWeb3React } from './index'
import useTransactionDeadline from './useTransactionDeadline'
import useENS from './useENS'
import { DiDiTxFee, DiDiTxFeeParams, queryTxFeeSwapTransaction } from '../utils/didi'
import { SwapForDiDi, SwapPayBody, SwapPayType } from '../utils/didiBridge'
import { useTranslation } from 'react-i18next'

/**
 * èŽ·å–didiçš„æœåŠ¡è´¹ç”¨
 * [çŸ¿å·¥è´¹] + [æœåŠ¡è´¹] > [ç”¨æˆ·ETHä½™é¢] ä¸å…è®¸å…‘æ¢
 */
export function useDiDiGas(
  trade: Trade | undefined,
  swapCall: SwapCall[],
  allowedSlippage: number = INITIAL_ALLOWED_SLIPPAGE, // in bips
  inputError: string | undefined
): { error: string | undefined; txFee: DiDiTxFee | undefined } | undefined {
  const [txFeeParams, setTxFeeParams] = useState<DiDiTxFeeParams | undefined>()
  const deadline = useTransactionDeadline() // custom from users settings
  // å¤šè¯­è¨€
  const { t } = useTranslation()
  const pct = basisPointsToPercent(allowedSlippage)
  // å¯¹æ¯”å‚æ•°æ˜¯å¦æ”¹å˜
  useEffect(() => {
    const address = localStorage.getItem('address')
    if (trade && address && trade.inputAmount && trade.outputAmount && deadline && swapCall && swapCall[0]) {
      // console.log('useDiDiGas trade', trade)
      const swapContractPath: string[] = []
      trade.route.path.forEach(p => swapContractPath.push(p.address))
      const newParams: DiDiTxFeeParams = {
        swapContractPath: swapContractPath.join(','),
        // amountIn: trade.inputAmount.toSignificant(trade.inputAmount.currency.decimals),
        // amountOut: trade.outputAmount.toSignificant(trade.outputAmount.currency.decimals),
        amountIn:
          trade.tradeType === TradeType.EXACT_INPUT
            ? trade.inputAmount.toSignificant(trade.inputAmount.currency.decimals)
            : trade.maximumAmountIn(pct).toSignificant(trade.inputAmount.currency.decimals),
        amountOut:
          trade.tradeType === TradeType.EXACT_INPUT
            ? trade.minimumAmountOut(pct).toSignificant(trade.outputAmount.currency.decimals)
            : trade.outputAmount.toSignificant(trade.outputAmount.currency.decimals),
        methodName: swapCall[0].parameters.methodName,
        tokenIn: trade.inputAmount?.currency instanceof Token ? trade.inputAmount.currency.address : '',
        tokenOut: trade.outputAmount?.currency instanceof Token ? trade.outputAmount.currency.address : '',
        address: address,
        deadline: deadline.toNumber()
      }
      // ç¬¬ä¸€æ¬¡è®¾ç½®å‚æ•°
      if (!txFeeParams) {
        setTxFeeParams(newParams)
      }
      // æ£€æŸ¥ä¸Šæ¬¡å‚æ•°æ˜¯å¦ç›¸åŒ
      else if (JSON.stringify(newParams) !== JSON.stringify(txFeeParams)) {
        setTxFeeParams(newParams)
      }
    } else {
      setTxFeeParams(undefined)
    }
  }, [pct, trade, deadline, swapCall, txFeeParams])
  const [error, setError] = useState<string | undefined>()
  // è¯·æ±‚æŽ¥å£èŽ·å–æœåŠ¡è´¹ç”¨
  const [txFee, setTxFee] = useState<DiDiTxFee | undefined>()

  useEffect(() => {
    if (txFeeParams) {
      setError(t('Calculating'))
      if (inputError) {
        setTxFee(undefined)
        return
      }
      queryTxFeeSwapTransaction(txFeeParams)
        .then(function(r) {
          setError(undefined)
          setTxFee(r)
        })
        .catch(e => {
          console.error('queryTxFeeSwapTransaction error', e)
          setError(t('Calculation Failed'))
          if (e.code === 'swap_contract_address_does_not_exist') {
            setError(t('TipModal6'))
          }
        })
    } else {
      setTxFee(undefined)
    }
  }, [t, txFeeParams, inputError])
  return useMemo(() => {
    return { error: error, txFee: txFee }
  }, [error, txFee])
}

export enum SwapCallbackState {
  INVALID,
  LOADING,
  VALID
}

export interface SwapCall {
  contract: Contract
  parameters: SwapParameters
}

/**
 * Returns the swap calls that can be used to make the trade
 * @param trade trade to execute
 * @param allowedSlippage user allowed slippage
 * @param recipientAddressOrName
 */
function useSwapCallArguments(
  trade: Trade | undefined, // trade to execute, required
  allowedSlippage: number = INITIAL_ALLOWED_SLIPPAGE, // in bips
  recipientAddressOrName: string | null // the ENS name or address of the recipient of the trade, or null if swap should be returned to sender
): SwapCall[] {
  const { account, chainId, library } = useActiveWeb3React()

  const { address: recipientAddress } = useENS(recipientAddressOrName)
  const recipient = recipientAddressOrName === null ? account : recipientAddress
  const deadline = useTransactionDeadline()
  return useMemo(() => {
    if (!trade || !recipient || !library || !account || !chainId || !deadline) return []

    const contract: Contract | null = getRouterContract(chainId, library, account)
    if (!contract) {
      return []
    }
    const swapMethods = [
      Router.swapCallParameters(trade, {
        feeOnTransfer: false,
        allowedSlippage: new Percent(JSBI.BigInt(allowedSlippage), BIPS_BASE),
        recipient,
        deadline: deadline.toNumber()
      })
    ]
    // åœ¨å…¶å®ƒå¸å…‘æ¢æˆETHçš„æ—¶å€™ä¼šå¤šä¸€ä¸ªswapExactTokensForETHSupportingFeeOnTransferTokens æš‚æ—¶ä¸æ¸…æ¥šç”¨å¤„
    return swapMethods.map(parameters => ({ parameters, contract }))
  }, [account, allowedSlippage, chainId, deadline, library, recipient, trade])
}

// returns a function that will execute a swap, if the parameters are all valid
// and the user has approved the slippage adjusted input amount for the trade
export function useSwapCallback(
  trade: Trade | undefined, // trade to execute, required
  allowedSlippage: number = INITIAL_ALLOWED_SLIPPAGE, // in bips
  recipientAddressOrName: string | null // the ENS name or address of the recipient of the trade, or null if swap should be returned to sender
): {
  state: SwapCallbackState
  callback: null | ((txFee: DiDiTxFee) => Promise<string>)
  error: string | null
  swapCalls: SwapCall[]
} {
  const { account, chainId, library } = useActiveWeb3React()
  const swapCalls = useSwapCallArguments(trade, allowedSlippage, recipientAddressOrName)

  const { address: recipientAddress } = useENS(recipientAddressOrName)
  const recipient = recipientAddressOrName === null ? account : recipientAddress
  const deadline = useTransactionDeadline()

  return useMemo(() => {
    if (!trade || !library || !account || !chainId || !deadline) {
      return { state: SwapCallbackState.INVALID, callback: null, error: 'Missing dependencies', swapCalls: swapCalls }
    }
    if (!recipient) {
      if (recipientAddressOrName !== null) {
        return { state: SwapCallbackState.INVALID, callback: null, error: 'Invalid recipient', swapCalls: swapCalls }
      } else {
        return { state: SwapCallbackState.LOADING, callback: null, error: null, swapCalls: swapCalls }
      }
    }
    return {
      state: SwapCallbackState.VALID,
      callback: function onSwap(txGas: DiDiTxFee): Promise<string> {
        return new Promise<string>(function(resolve, reject) {
          const swapContractPath: string[] = []
          const pct = basisPointsToPercent(allowedSlippage)
          trade.route.path.forEach(p => swapContractPath.push(p.address))
          const params: SwapPayBody = {
            sourceId: uuid(),
            swapContractPath: swapContractPath.join(','),
            contractAddressa: trade.inputAmount?.currency instanceof Token ? trade.inputAmount.currency.address : '',
            contractAddressb: trade.outputAmount?.currency instanceof Token ? trade.outputAmount.currency.address : '',
            methodName: swapCalls[0].parameters.methodName,
            amounta:
              trade.tradeType === TradeType.EXACT_INPUT
                ? trade.inputAmount.toSignificant(trade.inputAmount.currency.decimals)
                : trade.maximumAmountIn(pct).toSignificant(trade.inputAmount.currency.decimals),
            amountb:
              trade.tradeType === TradeType.EXACT_INPUT
                ? trade.minimumAmountOut(pct).toSignificant(trade.outputAmount.currency.decimals)
                : trade.outputAmount.toSignificant(trade.outputAmount.currency.decimals),
            minerFee: txGas.gasFee,
            serviceFee: txGas.serviceFee,
            gasPrice: txGas.gasPrice,
            deadline: deadline.toNumber(),
            contractAddressAScale: trade.inputAmount.currency.decimals,
            contractAddressBScale: trade.outputAmount.currency.decimals,
            feeScale: 18,
            symbolA: trade.inputAmount.currency.symbol,
            symbolB: trade.outputAmount.currency.symbol,
            symbolFee: 'ETH'
            // ...other
          }
          SwapForDiDi(SwapPayType.SWAP0001, params)
            .then(function(res) {
              //  é€»è¾‘åˆ¤æ–­
              resolve(JSON.stringify(res))
            })
            .catch(e => {
              reject({ message: e?.msg?.toString() })
            })
        })
      },
      error: null,
      swapCalls: swapCalls
    }
  }, [trade, library, account, chainId, deadline, recipient, swapCalls, recipientAddressOrName, allowedSlippage])
}
