import i18next from 'i18next'
import { initReactI18next } from 'react-i18next'
import XHR from 'i18next-xhr-backend'
import LanguageDetector from 'i18next-browser-languagedetector'
import bridge from './utils/bridge'

bridge()
declare global {
  interface Window {
    JSBridge: {
      invoke: (bridgeName: string, data?: { [p: string]: any }, callback?: (res: any, error: any) => void) => void
      receiveMessage: () => void
      register: () => void
    }
  }
}

window.JSBridge.invoke('getLanguage', {}, res => {
  if (res === 'zh-cn') {
    localStorage.language = 'zh-CN'
  } else {
    localStorage.language = 'en-US'
  }
  i18next.changeLanguage(localStorage.language, (err, t) => {
    // resources have been loaded
    console.log('changeLanguage success: ' + res)
    t(localStorage.language)
  })
})

i18next
  .use(XHR)
  .use(LanguageDetector)
  .use(initReactI18next)
  .init({
    backend: {
      loadPath: `./locales/{{lng}}.json`
    },
    react: {
      useSuspense: true
    },
    fallbackLng: 'es-US',
    preload: ['es-US'],
    keySeparator: false,
    interpolation: { escapeValue: false }
  })

export default i18next
