import Axios from 'axios'
import qs from 'qs'
import i18next from 'i18next'
const axios: any = Axios.create({
  baseURL: process.env.REACT_APP_HOST,
  transformRequest: [
    function(data: { [key: string]: any }) {
      return qs.stringify(data)
    }
  ]
})

window.JSBridge.invoke('login', {}, res => {
  localStorage.token = res.token
})

// ç”¨æˆ·Tokenéœ€è¦ä»ŽAPPèŽ·å–
// export const token =
//   'eyJhbGciOiJIUzUxMiJ9..LJFEy14a7xdvo13ofvnZugmwyDRD5ku2aSaZ1tnxV9cFytMVVoSe0wtlU3A7Tn_22upXrkSzdTYKY8AJ7W8E0A'

// APIé»˜è®¤è¯­è¨€
const defaultLanguage = 'en'
// APIæ”¯æŒçš„è¯­è¨€
const supportLanguage: { [key: string]: string } = {
  //è‹±æ–‡
  en: 'en',
  'en-us': 'en',
  //éŸ©è¯­
  'ko-kr': 'ko-kr',
  //ä¿„è¯­
  'ru-ru': 'ru-ru',
  //ä¸­æ–‡
  zh: 'zh-cn',
  'zh-cn': 'zh-cn',
  'zh-tw': 'zh-tw'
}
axios.interceptors.request.use((config: any) => {
  config.headers.authorization = localStorage.token
  config.headers['Content-Language'] = supportLanguage[i18next.language?.toLocaleLowerCase()] || defaultLanguage
  return config
})

// // æ·»åŠ å“åº”æ‹¦æˆªå™¨
axios.interceptors.response.use(
  function(response: any) {
    // å¯¹å“åº”æ•°æ®åšç‚¹ä»€ä¹ˆ
    if (response.data.code !== '200') {
      console.error(response.data.msg)
    }
    return response.data
  },
  function(error: any) {
    // å¯¹å“åº”é”™è¯¯åšç‚¹ä»€ä¹ˆ
    return Promise.reject(error)
  }
)

export default axios
