import React, { useCallback, useState } from 'react'
import Modal from '../CenterModal'
import { AutoColumn } from '../Column'
import { RowBetween } from '../Row'
import styled from 'styled-components'
import { TYPE } from '../../theme'
import { ButtonPrimary, ButtonGray } from '../../components/Button'
import Success from 'assets/images/success.png'
import Fail from 'assets/images/failed.png'
import { useTranslation } from 'react-i18next'
import axios from '../../axios'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
  background-color: ${({ theme }) => theme.allmodal};
  color: ${({ theme }) => theme.text1};
`
const Tips = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`
const Compelete = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justfy-content: center;
  align-items: center;
`
const Failed = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justfy-content: center;
  align-items: center;
`
interface StakingModalProps {
  isOpen: boolean
  onDismiss: () => void
  currency?: any
}

export default function TipModal({ isOpen, onDismiss, currency }: StakingModalProps) {
  const { t } = useTranslation()
  function wrappedOnDismiss() {
    onDismiss()
  }

  const [show, setShow] = useState('tips')
  const upCoin = useCallback(() => {
    // const input = event.target.value
    axios({
      url: '/getSwapCurrencyByContractAddress',
      params: {
        currencySearch: currency && currency.address
      }
    }).then((res: any) => {
      if (res.code === '200') {
        setShow('compelete')
      } else {
        setShow('failed')
      }
    })
  }, [currency])

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      <ContentWrapper gap="lg" style={{ padding: '20px 20px' }}>
        {show === 'tips' ? (
          <Tips>
            <TYPE.body style={{ marginBottom: '20px' }}>
              {t('TipModal1')}&nbsp;
              {currency && currency.symbol}
              {t('TipModal2')}&nbsp;
              {currency && currency.symbol}
              {t('TipModal3')}
            </TYPE.body>
            <RowBetween>
              <ButtonGray
                padding="8px"
                borderRadius="4px"
                width="120px"
                height="46px"
                onClick={onDismiss}
                style={{ background: '#ededed', color: '#000' }}
              >
                {t('cancel')}
              </ButtonGray>
              <ButtonPrimary padding="8px" borderRadius="4px" width="120px" height="46px" onClick={upCoin}>
                {t('confirm')}
              </ButtonPrimary>
            </RowBetween>
          </Tips>
        ) : null}

        {show === 'compelete' ? (
          <Compelete>
            <img src={Success} alt="" />
            <TYPE.body style={{ marginBottom: '20px' }}>
              {currency && currency.symbol}
              {t('TipModal4')}
            </TYPE.body>
            <ButtonPrimary padding="8px" borderRadius="4px" width="100%" height="46px" onClick={onDismiss}>
              {t('confirm')}
            </ButtonPrimary>
          </Compelete>
        ) : null}

        {show === 'failed' ? (
          <Failed>
            <img src={Fail} alt="" />
            <TYPE.body style={{ marginBottom: '20px' }}>
              {currency && currency.symbol}
              {t('TipModal5')}
            </TYPE.body>
            <ButtonPrimary padding="8px" borderRadius="4px" width="100%" height="46px" onClick={onDismiss}>
              {t('confirm')}
            </ButtonPrimary>
          </Failed>
        ) : null}
      </ContentWrapper>
    </Modal>
  )
}
