export enum SwapPayType {
  SWAP0001 = 'SWAP0001', // å…‘æ¢
  SWAP0002 = 'SWAP0002', // SWAP0002æ·»åŠ èµ„é‡‘æ± 
  SWAP0003 = 'SWAP0003' // ç§»é™¤èµ„é‡‘æ± 
}

/**
 * å…‘æ¢å‘èµ·
 * @param type
 * @param payBody
 */
export function SwapForDiDi(type: SwapPayType, payBody: SwapPayBody) {
  return new Promise(function(resolve, reject) {
    window.JSBridge.invoke(
      'SWAP',
      {
        type: type, // SWAP0001å…‘æ¢ SWAP0002æ·»åŠ èµ„é‡‘æ±  SWAP0003ç§»é™¤èµ„é‡‘æ± 
        data: payBody
      },
      (res, error) => {
        if (res) {
          if (res.code === '200') {
            resolve(res)
          } else {
            reject(res)
          }
        } else {
          if (error === 'cancel') {
            reject({ msg: i18next.t('cancel'), error: error })
          } else {
            reject({ msg: i18next.t('Network error'), error: error })
          }
        }
      }
    )
  })
}
