import React, { Suspense, useEffect } from 'react'
import { Route, Switch, Redirect } from 'react-router-dom'
import styled from 'styled-components'
import Swap from './Swap'
import Pool from './Pool/index'
import AddLiquidity from 'pages/AddLiquidity'
import Settings from 'pages/Settings'
import Record from 'pages/Record'
import LeadingPool from 'components/LeadingPool'
import WithDraw from 'components/Withdraw'
import Web3ReactManager from '../components/Web3ReactManager'
import { RedirectDuplicateTokenIds, RedirectOldAddLiquidityPathStructure } from 'pages/AddLiquidity/redirects'
import { RedirectOldRemoveLiquidityPathStructure } from './RemoveLiquidity/redirects'
import RemoveLiquidity from './RemoveLiquidity'
import axios from '../axios'
import { updateUserDarkMode } from 'state/user/actions'
import { useDispatch } from 'react-redux'
import { AppDispatch } from 'state'
import PoolFinder from 'pages/PoolFinder'

const AppWrapper = styled.div`
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  overflow-x: hidden;
  height: 100%;
`
const BodyWrapper = styled.div`
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  z-index: 10;
  min-height: calc(100vh - 4px);
  background: ${({ theme }) => theme.bg2};

  ${({ theme }) => theme.mediaWidth.upToSmall`
    background: ${({ theme }) => theme.bg2};
  `};

  z-index: 1;
`

export default function App() {
  const dispatch = useDispatch<AppDispatch>()
  window.JSBridge.invoke('getTheme', {}, res => {
    localStorage.darkMode = res === 'dark' ? '1' : '0'
    dispatch(updateUserDarkMode({ userDarkMode: localStorage.darkMode === '1' ? true : false }))
  })
  useEffect(() => {
    ;(async () => {
      const res = await axios({
        url: '/getRechargeAddress/didi',
        method: 'post',
        data: {
          currency: 'ETH'
        }
      })
      localStorage.address = res.data.address
      // localStorage.address = '0x83863e1f32cc0d214c54AF56D68548339d07bf57'
    })()
  }, [])
  return (
    <Suspense fallback={null}>
      <AppWrapper>
        <Web3ReactManager>
          <BodyWrapper>
            <Switch>
              <Route exact strict path="/swap" component={Swap} />
              <Route exact strict path="/pool" component={Pool} />
              <Route exact strict path="/find" component={PoolFinder} />
              <Route exact strict path="/add" component={AddLiquidity} />
              <Route exact path="/add/:currencyIdA" component={RedirectOldAddLiquidityPathStructure} />
              <Route exact path="/add/:currencyIdA/:currencyIdB" component={RedirectDuplicateTokenIds} />
              <Route exact strict path="/settings" component={Settings} />
              <Route exact strict path="/record" component={Record} />
              <Route exact strict path="/leadingpool" component={LeadingPool} />
              <Route exact strict path="/withdraw" component={WithDraw} />
              <Route exact strict path="/remove/:tokens" component={RedirectOldRemoveLiquidityPathStructure} />
              <Route exact strict path="/remove/:currencyIdA/:currencyIdB" component={RemoveLiquidity} />
              <Redirect to="/swap" />
            </Switch>
          </BodyWrapper>
        </Web3ReactManager>
      </AppWrapper>
    </Suspense>
  )
}
