import { Currency, CurrencyAmount, currencyEquals, Token, TokenAmount, WETH } from '@iccswap/sdk'
import React, { useCallback, useEffect, useMemo, useState, useContext } from 'react'
import ADD from 'assets/images/lock.png'
import styled from 'styled-components'
import AppFooter from '../AppFooter'
import { RouteComponentProps } from 'react-router-dom'
import { Text } from 'rebass'
import { ButtonError, ButtonLight } from '../../components/Button'
import { ThemeContext } from 'styled-components'
import { BlueCard } from '../../components/Card'
import { AutoColumn, ColumnCenter } from '../../components/Column'
import TransactionConfirmationModal, {
  ConfirmationModalContent,
  TransactionErrorContent
} from '../../components/TransactionConfirmationModal'
import { CurrencyInputPanelDiDi } from '../../components/CurrencyInputPanel'
import { MinimalPositionCard } from '../../components/PositionCard'
import { RowFlat, RowCenter } from '../../components/Row'
import { RowBetween, RowFixed, RowFlex, RowColumn } from '../../components/Row'
import CurrencyLogo from '../../components/CurrencyLogo'
import { ButtonPrimary } from '../../components/Button'
import arrow from '../../assets/images/arrow-tail-down.png'
import Switch from 'assets/images/switch.png'
import Tips from 'assets/images/tishi.png'
// import QuestionHelper from 'components/QuestionHelper'
import { useTranslation } from 'react-i18next'

import { PairState } from '../../data/Reserves'
import { useActiveWeb3React } from '../../hooks'
import { useCurrency } from '../../hooks/Tokens'
import useTransactionDeadline from '../../hooks/useTransactionDeadline'
import { useWalletModalToggle } from '../../state/application/hooks'
import { Field } from '../../state/mint/actions'
import { useDerivedMintInfo, useMintActionHandlers, useMintState } from '../../state/mint/hooks'

import { useIsExpertMode, useUserSlippageTolerance } from '../../state/user/hooks'
import { TYPE } from '../../theme'
import { maxAmountSpend } from '../../utils/maxAmountSpend'
import AppBody from '../AppBody'
import { Wrapper } from '../Pool/styleds'
// import { ConfirmAddModalBottom } from './ConfirmAddModalBottom'
import { currencyId } from '../../utils/currencyId'
import { PoolPriceBar } from './PoolPriceBar'
import axios from '../../axios'
import Axios from 'axios'
import Header from 'components/Header'
import crypto from 'crypto'
import { calculateSlippageAmount, uuid } from 'utils/index'
import { judgeClient } from 'utils/bridge'

export interface Txfee {
  gasFee: string
  gasLimit: number
  gasPrice: string
  serviceFee: string
}

const Circle = styled.div`
  color: ${({ theme }) => theme.text1};
  background: ${({ theme }) => theme.bg4};
  border-radius: 50%;
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
`
const WarningTips = styled.div`
  width: 100%;
  height: 40px;
  border-radius: 24px;
  background: #eddcd9;
  color: rgba(255, 112, 76, 100);
  border: 1px solid rgba(211, 94, 67, 100);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
`

export default function AddLiquidity({
  match: {
    params: { currencyIdA, currencyIdB }
  },
  history
}: RouteComponentProps<{ currencyIdA?: string; currencyIdB?: string }>) {
  const { account, chainId, library } = useActiveWeb3React()

  const currencyA = useCurrency(currencyIdA)
  const currencyB = useCurrency(currencyIdB)

  const { t } = useTranslation()

  const oneCurrencyIsWETH = Boolean(
    chainId &&
      ((currencyA && currencyEquals(currencyA, WETH[chainId])) ||
        (currencyB && currencyEquals(currencyB, WETH[chainId])))
  )

  const toggleWalletModal = useWalletModalToggle() // toggle wallet when disconnected

  const expertMode = useIsExpertMode()

  // mint state
  const { independentField, typedValue, otherTypedValue } = useMintState()
  const {
    dependentField,
    currencies,
    pair,
    pairState,
    currencyBalances,
    parsedAmounts,
    price,
    noLiquidity,
    liquidityMinted,
    poolTokenPercentage,
    error
  } = useDerivedMintInfo(currencyA ?? undefined, currencyB ?? undefined)
  const { onFieldAInput, onFieldBInput } = useMintActionHandlers(noLiquidity)

  const isValid = !error

  // modal and loading
  const [showConfirm, setShowConfirm] = useState<boolean>(false)
  const [showState, setShowState] = useState<boolean>(true)
  const [attemptingTxn, setAttemptingTxn] = useState<boolean>(false) // clicked confirm

  // txn values
  const deadline = useTransactionDeadline() // custom from users settings
  const [allowedSlippage] = useUserSlippageTolerance() // custom from users
  const [txHash, setTxHash] = useState<string>('')
  const [errMsg, setErrMsg] = useState<string>()
  // console.log('allowedSlippage', allowedSlippage, (10000 - allowedSlippage) / 10000)

  // get formatted amounts
  const formattedAmounts = useMemo(() => {
    return {
      [independentField]: typedValue,
      [dependentField]: noLiquidity
        ? otherTypedValue
        : parsedAmounts[dependentField]?.toSignificant(parsedAmounts[dependentField]?.currency.decimals) ?? ''
    }
  }, [dependentField, independentField, noLiquidity, otherTypedValue, parsedAmounts, typedValue])
  const isEth = (cur?: CurrencyAmount) => {
    if (typeof cur === 'undefined') return null
    if (cur.currency instanceof Token) {
      return cur.currency.address
    }
    return null
  }
  const methodName = useCallback(() => {
    const curA = parsedAmounts.CURRENCY_A
    const curB = parsedAmounts.CURRENCY_B
    if (typeof curA === 'undefined' || typeof curB === 'undefined') {
      return
    }
    if (curA.currency.symbol === 'ETH' || curB.currency.symbol === 'ETH') {
      return 'addLiquidityETH'
    } else {
      return 'addLiquidity'
    }
  }, [parsedAmounts.CURRENCY_A, parsedAmounts.CURRENCY_B])

  const [txfee, setTxfee] = useState<Txfee>()
  const [balance, setBalance] = useState<string>()
  useEffect(() => {
    ;(async () => {
      const res = await axios({
        url: '/getUserCurrencyBalance',
        method: 'post',
        data: {
          contractAddress: ''
        }
      })
      setBalance(res.data.balance)
    })()
  }, [])
  useEffect(() => {
    if (!(parsedAmounts.CURRENCY_A instanceof CurrencyAmount && parsedAmounts.CURRENCY_B instanceof CurrencyAmount)) {
      return
    }
    if (txfee) return
    const { [Field.CURRENCY_A]: parsedAmountA, [Field.CURRENCY_B]: parsedAmountB } = parsedAmounts

    const amountsMin = {
      [Field.CURRENCY_A]: calculateSlippageAmount(parsedAmountA, noLiquidity ? 0 : allowedSlippage)[0],
      [Field.CURRENCY_B]: calculateSlippageAmount(parsedAmountB, noLiquidity ? 0 : allowedSlippage)[0]
    }

    const timeout = setTimeout(async () => {
      const res = await axios({
        url: '/queryTxFeeAddLiquidity',
        method: 'post',
        data: {
          addressOut: localStorage.address,
          methodName: methodName(),
          tokenA: isEth(parsedAmounts.CURRENCY_A), //ä¸º â€œETHâ€æ—¶ä¼ null
          tokenB: isEth(parsedAmounts.CURRENCY_B),
          amountADesired: formattedAmounts['CURRENCY_A'],
          amountBDesired: formattedAmounts['CURRENCY_B'],
          amountAMin:
            parsedAmountA.currency instanceof Token
              ? new TokenAmount(parsedAmountA.currency, amountsMin[Field.CURRENCY_A]).toSignificant(
                  parsedAmounts.CURRENCY_A?.currency.decimals
                )
              : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
          amountBMin:
            parsedAmountB.currency instanceof Token
              ? new TokenAmount(parsedAmountB.currency, amountsMin[Field.CURRENCY_B]).toSignificant(
                  parsedAmounts.CURRENCY_B?.currency.decimals
                )
              : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
          deadline: deadline?.toNumber() //æˆªæ­¢æ—¶é—´(æ—¶é—´æˆ³) å•ä½ï¼šç§’
        }
      })
      setTxfee(res.data)
    }, 1000)
    return () => {
      clearTimeout(timeout)
    }
  }, [
    parsedAmounts,
    noLiquidity,
    allowedSlippage,
    currencies,
    formattedAmounts,
    deadline,
    dependentField,
    independentField,
    methodName,
    parsedAmounts.CURRENCY_A,
    parsedAmounts.CURRENCY_B,
    txfee
  ])

  // get the max amounts user can add
  const maxAmounts: { [field in Field]?: TokenAmount } = [Field.CURRENCY_A, Field.CURRENCY_B].reduce(
    (accumulator, field) => {
      return {
        ...accumulator,
        [field]: maxAmountSpend(currencyBalances[field])
      }
    },
    {}
  )

  const atMaxAmounts: { [field in Field]?: TokenAmount } = [Field.CURRENCY_A, Field.CURRENCY_B].reduce(
    (accumulator, field) => {
      return {
        ...accumulator,
        [field]: maxAmounts[field]?.equalTo(parsedAmounts[field] ?? '0')
      }
    },
    {}
  )

  // check whether the user has approved the router on the tokens
  // const [approvalA, approveACallback] = useApproveCallback(parsedAmounts[Field.CURRENCY_A], ROUTER_ADDRESS)
  // const [approvalB, approveBCallback] = useApproveCallback(parsedAmounts[Field.CURRENCY_B], ROUTER_ADDRESS)

  async function onAdd() {
    if (!chainId || !library || !account) return

    const { [Field.CURRENCY_A]: parsedAmountA, [Field.CURRENCY_B]: parsedAmountB } = parsedAmounts
    if (!parsedAmountA || !parsedAmountB || !currencyA || !currencyB || !deadline) {
      return
    }
    setAttemptingTxn(true)

    const amountsMin = {
      [Field.CURRENCY_A]: calculateSlippageAmount(parsedAmountA, noLiquidity ? 0 : allowedSlippage)[0],
      [Field.CURRENCY_B]: calculateSlippageAmount(parsedAmountB, noLiquidity ? 0 : allowedSlippage)[0]
    }

    const curClient = judgeClient()
    if (curClient === 'pc') {
      const md5 = crypto.createHash('md5')
      const md5Pass = crypto.createHash('md5')
      const res = await Axios({
        url: process.env.REACT_APP_HOST + '/payAccount',
        method: 'post',
        data: {
          businessCode: 'SWAP',
          bizType: 'SWAP0002',
          payBody: {
            sourceId: uuid(),
            contractAddressa: isEth(parsedAmounts.CURRENCY_A),
            contractAddressb: isEth(parsedAmounts.CURRENCY_B),
            methodName: methodName(),
            amounta: formattedAmounts['CURRENCY_A'],
            amountb: formattedAmounts['CURRENCY_B'],
            minAmounta:
              parsedAmountA.currency instanceof Token
                ? new TokenAmount(parsedAmountA.currency, amountsMin[Field.CURRENCY_A]).toSignificant(
                    parsedAmounts.CURRENCY_A?.currency.decimals
                  )
                : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
            minAmountb:
              parsedAmountB.currency instanceof Token
                ? new TokenAmount(parsedAmountB.currency, amountsMin[Field.CURRENCY_B]).toSignificant(
                    parsedAmounts.CURRENCY_B?.currency.decimals
                  )
                : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
            minerFee: txfee?.gasFee,
            serviceFee: txfee?.serviceFee,
            gasPrice: txfee?.gasPrice,
            deadline: deadline?.toNumber(),
            contractAddressAScale: parsedAmounts.CURRENCY_A?.currency.decimals,
            contractAddressBScale: parsedAmounts.CURRENCY_B?.currency.decimals,
            feeScale: 18,
            symbolA: parsedAmounts.CURRENCY_A?.currency.symbol,
            symbolB: parsedAmounts.CURRENCY_B?.currency.symbol,
            symbolFee: 'ETH'
          },
          sign: md5.update('bizType=SWAP0002&businessCode=SWAP&key=nEGMOv3VJAds69eg').digest('hex'),
          accountPassword: md5Pass.update('123456').digest('hex')
        },
        headers: {
          'Content-Language': 'zh-cn',
          authorization: localStorage.token
        }
      })

      if (res.data.code === '200') {
        setAttemptingTxn(false)
        setTxHash(res.data.msg)
      } else {
        setAttemptingTxn(false)
        setErrMsg(res.data.msg)
      }
    } else {
      window.JSBridge.invoke(
        'SWAP',
        {
          type: 'SWAP0002', // SWAP0001å…‘æ¢ SWAP0002æ·»åŠ èµ„é‡‘æ±  SWAP0003ç§»é™¤èµ„é‡‘æ± 
          data: {
            sourceId: uuid(),
            contractAddressa: isEth(parsedAmounts.CURRENCY_A),
            contractAddressb: isEth(parsedAmounts.CURRENCY_B),
            methodName: methodName(),
            amounta: formattedAmounts['CURRENCY_A'],
            amountb: formattedAmounts['CURRENCY_B'],
            minAmounta:
              parsedAmountA.currency instanceof Token
                ? new TokenAmount(parsedAmountA.currency, amountsMin[Field.CURRENCY_A]).toSignificant(
                    parsedAmounts.CURRENCY_A?.currency.decimals
                  )
                : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
            minAmountb:
              parsedAmountB.currency instanceof Token
                ? new TokenAmount(parsedAmountB.currency, amountsMin[Field.CURRENCY_B]).toSignificant(
                    parsedAmounts.CURRENCY_B?.currency.decimals
                  )
                : TokenAmount.ether(amountsMin[Field.CURRENCY_A]).toSignificant(18),
            minerFee: txfee?.gasFee,
            serviceFee: txfee?.serviceFee,
            gasPrice: txfee?.gasPrice,
            deadline: deadline?.toNumber(),
            contractAddressAScale: parsedAmounts.CURRENCY_A?.currency.decimals,
            contractAddressBScale: parsedAmounts.CURRENCY_B?.currency.decimals,
            feeScale: 18,
            symbolA: parsedAmounts.CURRENCY_A?.currency.symbol,
            symbolB: parsedAmounts.CURRENCY_B?.currency.symbol,
            symbolFee: 'ETH'
          }
        },
        (res, error?: string) => {
          if (error === 'cancel') {
            setAttemptingTxn(false)
            return
          }
          try {
            if (res.code === '200') {
              setAttemptingTxn(false)
              setTxHash(res.msg)
            } else {
              setAttemptingTxn(false)
              // setErrMsg(res.msg)
            }
          } catch (error) {
            console.error(error)
          }
        }
      )
    }
  }
  const theme = useContext(ThemeContext)
  const modalHeader = () => {
    return (
      <AutoColumn gap="20px">
        <RowBetween>
          <RowFlex>
            <CurrencyLogo currency={currencies[Field.CURRENCY_A]} style={{ marginRight: '8px' }} />
            <TYPE.body>{currencies[Field.CURRENCY_A]?.symbol}</TYPE.body>
          </RowFlex>
          <RowFixed>
            <TYPE.body>{parsedAmounts[Field.CURRENCY_A]?.toSignificant(6)}</TYPE.body>
          </RowFixed>
        </RowBetween>
        <RowBetween>
          <RowFlex>
            <CurrencyLogo currency={currencies[Field.CURRENCY_B]} style={{ marginRight: '8px' }} />
            <TYPE.body>{currencies[Field.CURRENCY_B]?.symbol}</TYPE.body>
          </RowFlex>
          <RowFixed>
            <TYPE.body>{parsedAmounts[Field.CURRENCY_B]?.toSignificant(6)}</TYPE.body>
          </RowFixed>
        </RowBetween>
        <RowCenter>
          <img width={14} height={20} src={arrow} alt="" />
        </RowCenter>

        <RowBetween style={{ padding: '0 0 15px 0px' }}>
          <RowFlat style={{ alignItems: 'center' }}>
            <CurrencyLogo currency={currencies[Field.CURRENCY_A]} style={{ marginRight: '8px' }} />
            <CurrencyLogo currency={currencies[Field.CURRENCY_B]} style={{ marginRight: '8px' }} />
            <Text fontSize="16px">
              {currencies[Field.CURRENCY_A]?.symbol + '/' + currencies[Field.CURRENCY_B]?.symbol}
            </Text>
          </RowFlat>
          <Text fontSize="16px" fontWeight={400}>
            {liquidityMinted?.toSignificant(6)}
          </Text>
        </RowBetween>

        <RowColumn
          style={{
            padding: '15px',
            background: `${theme.search}`,
            borderRadius: '6px',
            justifyContent: 'center',
            marginTop: '12px'
          }}
        >
          <RowBetween>
            <TYPE.body>{t('Price')}</TYPE.body>
            <TYPE.body style={{ display: 'flex', alignItems: 'center' }}>
              {showState
                ? `1 ${currencies[Field.CURRENCY_A]?.symbol} = ${price?.toSignificant(4)} ${
                    currencies[Field.CURRENCY_B]?.symbol
                  }`
                : `1 ${currencies[Field.CURRENCY_B]?.symbol} = ${price?.invert().toSignificant(4)} ${
                    currencies[Field.CURRENCY_A]?.symbol
                  }`}
              <img
                onClick={() => setShowState(!showState)}
                style={{ marginLeft: '10px', width: '16px' }}
                src={Switch}
                alt=""
              />
            </TYPE.body>
          </RowBetween>
          <RowBetween style={{ margin: '10px 0' }}>
            <TYPE.body style={{ display: 'flex' }}>
              {t('Share of Pool')}
              {/*<QuestionHelper text={t('The difference between the market price and your price due to trade size.')} />*/}
            </TYPE.body>
            <TYPE.body>{noLiquidity ? '100' : poolTokenPercentage?.toSignificant(4)}%</TYPE.body>
          </RowBetween>
          <RowBetween>
            <TYPE.body>{t('Liquidity Provider Fee')}</TYPE.body>
            <TYPE.body>{txfee?.gasFee} ETH</TYPE.body>
          </RowBetween>

          <TYPE.italic fontSize={12} textAlign="left" padding={'8px 0 0 0 '}>
            {`${t('over1')} ${allowedSlippage / 100}% ${t('over2')}`}
          </TYPE.italic>
          <ButtonPrimary style={{ margin: '20px 0 0 0' }} onClick={onAdd}>
            <Text fontWeight={500} fontSize={20}>
              {t('Done')}
            </Text>
          </ButtonPrimary>
        </RowColumn>
      </AutoColumn>
    )
  }

  const modalBottom = () => {
    return <></>
  }

  const pendingText = `${t('Supplying')} ${parsedAmounts[Field.CURRENCY_A]?.toSignificant(6)} ${
    currencies[Field.CURRENCY_A]?.symbol
  } ${t('and')} ${parsedAmounts[Field.CURRENCY_B]?.toSignificant(6)} ${currencies[Field.CURRENCY_B]?.symbol}`

  const handleCurrencyASelect = useCallback(
    (currencyA: Currency) => {
      onFieldAInput('')
      const newCurrencyIdA = currencyId(currencyA)
      if (newCurrencyIdA === currencyIdB) {
        history.push(`/add/${currencyIdB}/${currencyIdA}`)
      } else {
        history.push(`/add/${newCurrencyIdA}/${currencyIdB}`)
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currencyIdB, history, currencyIdA]
  )
  const handleCurrencyBSelect = useCallback(
    (currencyB: Currency) => {
      onFieldBInput('')
      const newCurrencyIdB = currencyId(currencyB)
      if (currencyIdA === newCurrencyIdB) {
        if (currencyIdB) {
          history.push(`/add/${currencyIdB}/${newCurrencyIdB}`)
        } else {
          history.push(`/add/${newCurrencyIdB}`)
        }
      } else {
        history.push(`/add/${currencyIdA ? currencyIdA : 'ETH'}/${newCurrencyIdB}`)
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [currencyIdA, history, currencyIdB]
  )

  const handleDismissConfirmation = useCallback(() => {
    setShowConfirm(false)
    // if there was a tx hash, we want to clear the input
    if (txHash) {
      onFieldAInput('')
    }
    setTxHash('')
    setErrMsg(undefined)
  }, [onFieldAInput, txHash])
  const query = new URLSearchParams(window.location.href.substring(window.location.href.indexOf('?')))
  const url = query.get('url')
  useEffect(() => {
    onFieldAInput('')
    onFieldBInput('')
  }, [url, onFieldAInput, onFieldBInput])

  const isCreate = history.location.pathname.includes('/create')

  const isError = () => {
    if (error) {
      return error
    }
    // è®¡ç®—å‡ºæ‰‹ç»­è´¹åŽ
    if (typeof txfee?.gasFee === 'string') {
      // æ‰‹ç»­è´¹è¶³å¤Ÿ
      if (Number(balance) > Number(txfee?.gasFee)) {
        // åˆ¤æ–­æ˜¯å¦ä»£å¸ä¸­ç”¨æˆ·è¾“å…¥çš„Aå¸æ˜¯å¦ä¸ºETH
        if (isEth(parsedAmounts.CURRENCY_A) === null) {
          if (Number(txfee?.gasFee) + Number(balance) > Number(formattedAmounts['CURRENCY_A'])) {
            return true
          }
        } else if (isEth(parsedAmounts.CURRENCY_B) === null) {
          // åˆ¤æ–­æ˜¯å¦ä»£å¸ä¸­ç”¨æˆ·è¾“å…¥çš„Bå¸æ˜¯å¦ä¸ºETH
          if (Number(txfee?.gasFee) + Number(balance) > Number(formattedAmounts['CURRENCY_B'])) {
            return true
          }
        }
        // ä¸¤ä¸ªå¸ç§æ²¡æœ‰ETH
        return true
      } else {
        // æ‰‹ç»­è´¹ä¸è¶³
        return 'æ‰‹ç»­è´¹ä¸è¶³'
      }
    } else {
      return `${t('calculating')}`
    }
  }
  return (
    <>
      <Header
        title={t('add')}
        right={t('AddQuestion')}
        back={() => {
          const query = new URLSearchParams(window.location.href.substring(window.location.href.indexOf('?')))
          const url = query.get('url')
          if (url !== null) {
            window.location.href = url
          } else {
            history.push(`/pool`)
          }
        }}
      />
      <AppBody>
        <Wrapper>
          <TransactionConfirmationModal
            isOpen={showConfirm}
            onDismiss={handleDismissConfirmation}
            attemptingTxn={attemptingTxn}
            hash={txHash}
            content={() => (
              <>
                {typeof errMsg === 'undefined' ? (
                  <ConfirmationModalContent
                    title={noLiquidity ? `${t('create')}` : `${t('Confirm add')}`}
                    onDismiss={handleDismissConfirmation}
                    topContent={modalHeader}
                    bottomContent={modalBottom}
                  />
                ) : (
                  <TransactionErrorContent message={errMsg} onDismiss={handleDismissConfirmation} />
                )}
              </>
            )}
            pendingText={pendingText}
          />
          <AutoColumn gap="20px">
            {noLiquidity ||
              (isCreate && (
                <ColumnCenter>
                  <BlueCard>
                    <AutoColumn gap="10px">
                      <TYPE.link fontWeight={600} color={'primaryText1'}>
                        You are the first liquidity provider.
                      </TYPE.link>
                      <TYPE.link fontWeight={400} color={'primaryText1'}>
                        The ratio of tokens you add will set the price of this pool.
                      </TYPE.link>
                      <TYPE.link fontWeight={400} color={'primaryText1'}>
                        Once you are happy with the rate click supply to review.
                      </TYPE.link>
                    </AutoColumn>
                  </BlueCard>
                </ColumnCenter>
              ))}
            {noLiquidity ? (
              <WarningTips>
                <img style={{ width: '18px', height: '18px', marginRight: '5px' }} src={Tips} alt="" />
                {t('New pairs, The number you increase decide the price')}
              </WarningTips>
            ) : (
              ''
            )}
            <CurrencyInputPanelDiDi
              value={formattedAmounts[Field.CURRENCY_A]}
              onUserInput={onFieldAInput}
              onMax={() => {
                onFieldAInput(maxAmounts[Field.CURRENCY_A]?.toExact() ?? '')
              }}
              onCurrencySelect={handleCurrencyASelect}
              showMaxButton={!atMaxAmounts[Field.CURRENCY_A]}
              currency={currencies[Field.CURRENCY_A]}
              id="add-liquidity-input-tokena"
              showCommonBases={false}
            />
            <RowCenter>
              <Circle>
                <img width={18} src={ADD} alt="" />
              </Circle>
            </RowCenter>
            <CurrencyInputPanelDiDi
              value={formattedAmounts[Field.CURRENCY_B]}
              onUserInput={onFieldBInput}
              onCurrencySelect={handleCurrencyBSelect}
              onMax={() => {
                console.log(maxAmounts[Field.CURRENCY_B]?.toExact())
                onFieldBInput(maxAmounts[Field.CURRENCY_B]?.toExact() ?? '')
              }}
              showMaxButton={!atMaxAmounts[Field.CURRENCY_B]}
              currency={currencies[Field.CURRENCY_B]}
              id="add-liquidity-input-tokenb"
              showCommonBases={false}
            />
            {currencies[Field.CURRENCY_A] && currencies[Field.CURRENCY_B] && pairState !== PairState.INVALID && (
              <>
                {!noLiquidity ?? 'æ–°äº¤æ˜“å¯¹ï¼ä½ å¢žåŠ çš„æ•°é‡å°†å†³å®šäº¤æ˜“å¯¹çš„åˆå§‹ä»·æ ¼'}
                <PoolPriceBar
                  currencies={currencies}
                  poolTokenPercentage={poolTokenPercentage}
                  noLiquidity={noLiquidity}
                  price={price}
                  fee={txfee}
                />
                {/* <LightCard padding="0px" borderRadius={'20px'}>
                  <RowBetween padding="1rem">
                    <TYPE.subHeader fontWeight={500} fontSize={14}>
                      {noLiquidity ? 'æ–°äº¤æ˜“å¯¹ï¼ä½ å¢žåŠ çš„æ•°é‡å°†å†³å®šäº¤æ˜“å¯¹çš„åˆå§‹ä»·æ ¼' : 'ä»·æ ¼'}
                    </TYPE.subHeader>
                  </RowBetween>{' '}
                  <LightCard padding="1rem" borderRadius={'20px'}>
                    <PoolPriceBar
                      currencies={currencies}
                      poolTokenPercentage={poolTokenPercentage}
                      noLiquidity={noLiquidity}
                      price={price}
                    />
                  </LightCard>
                </LightCard> */}
              </>
            )}

            {!account ? (
              <ButtonLight onClick={toggleWalletModal}>Connect Wallet</ButtonLight>
            ) : (
              <AutoColumn gap={'md'}>
                <ButtonError
                  onClick={() => {
                    expertMode ? onAdd() : error ?? setShowConfirm(true)
                  }}
                  disabled={
                    (!isValid && !!parsedAmounts[Field.CURRENCY_A] && !!parsedAmounts[Field.CURRENCY_B]) ||
                    !!error ||
                    isError() !== true
                  }
                >
                  <Text fontSize={20} fontWeight={500}>
                    {isError() === true ? `${t('addnow')}` : isError()}
                    {/* {error ?? (typeof txfee?.gasFee === 'string' && Number(balance) > Number(txfee?.gasFee))
                        ? 'ç«‹å³æ·»åŠ '
                        : 'æ‰‹ç»­è´¹ä¸è¶³'} */}
                  </Text>
                </ButtonError>
              </AutoColumn>
            )}
          </AutoColumn>
        </Wrapper>
      </AppBody>

      {pair && !noLiquidity && pairState !== PairState.INVALID ? (
        <AutoColumn
          style={{
            minWidth: '20rem',
            width: '100%',
            maxWidth: '400px',
            marginTop: '1rem',
            marginBottom: '2rem',
            padding: '0 16px'
          }}
        >
          <MinimalPositionCard showUnwrapped={oneCurrencyIsWETH} pair={pair} />
        </AutoColumn>
      ) : null}
      <AppFooter />
    </>
  )
}
