import { BLOCKED_PRICE_IMPACT_NON_EXPERT } from '../constants'
import { CurrencyAmount, Fraction, JSBI, Percent, Price, TokenAmount, Trade } from '@iccswap/sdk'
import { ALLOWED_PRICE_IMPACT_HIGH, ALLOWED_PRICE_IMPACT_LOW, ALLOWED_PRICE_IMPACT_MEDIUM } from '../constants'
import { Field } from '../state/swap/actions'
import { basisPointsToPercent } from './index'

const BASE_FEE = new Percent(JSBI.BigInt(30), JSBI.BigInt(10000)) // 0.03% åƒåˆ†ä¹‹ä¸‰ æ‰‹ç»­è´¹
const ONE_HUNDRED_PERCENT = new Percent(JSBI.BigInt(10000), JSBI.BigInt(10000)) // 100%
const INPUT_FRACTION_AFTER_FEE = ONE_HUNDRED_PERCENT.subtract(BASE_FEE) // 99.97%
// è®¡ç®—æ‰‹ç»­è´¹
// computes price breakdown for the trade
export function computeTradePriceBreakdown(
  trade?: Trade
): { priceImpactWithoutFee?: Percent; realizedLPFee?: CurrencyAmount } {
  // for each hop in our trade, take away the x*y=k price impact from 0.3% fees
  // e.g. for 3 tokens/2 hops: 1 - ((1 - .03) * (1-.03))
  const realizedLPFee = !trade
    ? undefined
    : ONE_HUNDRED_PERCENT.subtract(
        trade.route.pairs.reduce<Fraction>(
          (currentFee: Fraction): Fraction => currentFee.multiply(INPUT_FRACTION_AFTER_FEE),
          ONE_HUNDRED_PERCENT
        )
      )
  // remove lp fees from price impact ä»·æ ¼å½±å“ä¸­ç§»é™¤åˆçº¦æ‰‹ç»­è´¹ç™¾åˆ†æ¯”
  const priceImpactWithoutFeeFraction = trade && realizedLPFee ? trade.priceImpact.subtract(realizedLPFee) : undefined

  // the x*y=k impact ä»·æ ¼å½±å“
  const priceImpactWithoutFeePercent = priceImpactWithoutFeeFraction
    ? new Percent(priceImpactWithoutFeeFraction?.numerator, priceImpactWithoutFeeFraction?.denominator)
    : undefined
  // the amount of the input that accrues to LPs
  const realizedLPFeeAmount =
    realizedLPFee &&
    trade &&
    (trade.inputAmount instanceof TokenAmount
      ? new TokenAmount(trade.inputAmount.token, realizedLPFee.multiply(trade.inputAmount.raw).quotient)
      : CurrencyAmount.ether(realizedLPFee.multiply(trade.inputAmount.raw).quotient))
  // å‡åŽ»didiæ‰‹ç»­è´¹  éœ€æ±‚æ”¹å˜ï¼Œä¸å†è®¡ç®—
  // const didiRealizedLPFeeAmount =
  //   realizedLPFee &&
  //   trade &&
  //   (trade.inputAmount instanceof TokenAmount
  //     ? new TokenAmount(trade.inputAmount.token, JSBI.subtract(trade.inputAmount.raw, JSBI.BigInt(1000)))
  //     : CurrencyAmount.ether(JSBI.subtract(trade.inputAmount.raw, JSBI.BigInt(1000))))
  // console.log('didiRealizedLPFeeAmount:', didiRealizedLPFeeAmount)
  return { priceImpactWithoutFee: priceImpactWithoutFeePercent, realizedLPFee: realizedLPFeeAmount }
}

// computes the minimum amount out and maximum amount in for a trade given a user specified allowed slippage in bips
export function computeSlippageAdjustedAmounts(
  trade: Trade | undefined,
  allowedSlippage: number
): { [field in Field]?: CurrencyAmount } {
  const pct = basisPointsToPercent(allowedSlippage)
  return {
    [Field.INPUT]: trade?.maximumAmountIn(pct),
    [Field.OUTPUT]: trade?.minimumAmountOut(pct)
  }
}

export function warningSeverity(priceImpact: Percent | undefined): 0 | 1 | 2 | 3 | 4 {
  if (!priceImpact?.lessThan(BLOCKED_PRICE_IMPACT_NON_EXPERT)) return 4
  if (!priceImpact?.lessThan(ALLOWED_PRICE_IMPACT_HIGH)) return 3
  if (!priceImpact?.lessThan(ALLOWED_PRICE_IMPACT_MEDIUM)) return 2
  if (!priceImpact?.lessThan(ALLOWED_PRICE_IMPACT_LOW)) return 1
  return 0
}

export function formatExecutionPrice(trade?: Trade, inverted?: boolean): string {
  if (!trade) {
    return ''
  }
  return inverted
    ? `${trade.executionPrice.invert().toSignificant(6)} ${trade.inputAmount.currency.symbol} = 1 ${
        trade.outputAmount.currency.symbol
      }`
    : `${trade.executionPrice.toSignificant(6)} ${trade.outputAmount.currency.symbol} = 1 ${
        trade.inputAmount.currency.symbol
      }`
}

export function formatExecutionPriceExtend(
  ePrice?: Price,
  inputAmount?: CurrencyAmount,
  outputAmount?: CurrencyAmount,
  inverted?: boolean
): string {
  if (!ePrice || !inputAmount || !outputAmount) {
    return ''
  }
  return inverted
    ? `${ePrice.invert().toSignificant(6)} ${inputAmount.currency.symbol} = 1 ${outputAmount.currency.symbol}`
    : `${ePrice.toSignificant(6)} ${outputAmount.currency.symbol} = 1 ${inputAmount.currency.symbol}`
}
