import React from 'react'
import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { TYPE } from '../../theme'
import { NavLink } from 'react-router-dom'
import { useTranslation } from 'react-i18next'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
  background-color: ${({ theme }) => theme.allmodal};
  color: ${({ theme }) => theme.text1};
`
const Line = styled.div`
  width: 100%;
  height: 5px;
  display: inline-block;
  border-top: 1px solid ${({ theme }) => theme.modalLine};
`
const LineTwo = styled.div`
  width: 100%;
  height: 5px;
  display: inline-block;
  border-top: 5px solid ${({ theme }) => theme.modalLine};
`
interface StakingModalProps {
  isOpen: boolean
  onDismiss: () => void
}

export default function RecordModal({ isOpen, onDismiss }: StakingModalProps) {
  // monitor call to help UI loading state

  const { t } = useTranslation()
  function wrappedOnDismiss() {
    onDismiss()
  }

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOnDismiss} maxHeight={90}>
      <ContentWrapper gap="lg" style={{ padding: '20px 0' }}>
        <NavLink
          to="/settings"
          style={{
            textDecoration: 'none',
            paddingBottom: '16px',
            textAlign: 'center'
          }}
        >
          <TYPE.body>{t('setting')}</TYPE.body>
        </NavLink>
        <Line></Line>
        <NavLink
          to="/record"
          style={{
            textDecoration: 'none',
            paddingBottom: '16px',
            textAlign: 'center'
          }}
        >
          <TYPE.body>{t('record')}</TYPE.body>
        </NavLink>
        <LineTwo></LineTwo>
        <TYPE.body style={{ fontWeight: 500, textAlign: 'center' }} onClick={onDismiss}>
          {t('cancel')}
        </TYPE.body>
      </ContentWrapper>
    </Modal>
  )
}
