import { Trade, TradeType } from '@iccswap/sdk'
import React, { useContext, useMemo } from 'react'
import { Text } from 'rebass'
import { ThemeContext } from 'styled-components'
import { TYPE } from '../../theme'
import { isAddress, shortenAddress } from '../../utils'
import { computeTradePriceBreakdown, warningSeverity } from '../../utils/prices'
import { AutoColumn } from '../Column'
import CurrencyLogo from '../CurrencyLogo'
import { RowBetween, RowFixed } from '../Row'
import { TruncatedText } from './styleds'
import ArrowTailDown from '../../assets/images/arrow-tail-down.png'

export default function SwapModalHeader({
  trade,
  recipient,
  showAcceptChanges
}: {
  trade: Trade
  recipient: string | null
  showAcceptChanges: boolean
}) {
  const { priceImpactWithoutFee } = useMemo(() => computeTradePriceBreakdown(trade), [trade])
  const priceImpactSeverity = warningSeverity(priceImpactWithoutFee)

  const theme = useContext(ThemeContext)

  return (
    <AutoColumn gap={'md'} style={{ marginTop: '20px' }}>
      <RowBetween align="flex-end">
        <RowFixed gap={'0px'}>
          <CurrencyLogo currency={trade.inputAmount.currency} size={'24px'} style={{ marginRight: '12px' }} />
          <TruncatedText fontSize={24} fontWeight={500}>
            {trade.inputAmount.currency.symbol}
          </TruncatedText>
        </RowFixed>
        <RowFixed gap={'0px'}>
          <Text
            fontSize={24}
            fontWeight={500}
            style={{ marginLeft: '10px' }}
            color={showAcceptChanges && trade.tradeType === TradeType.EXACT_OUTPUT ? theme.orange : ''}
          >
            {trade.inputAmount.toSignificant(6)}
          </Text>
        </RowFixed>
      </RowBetween>
      <RowFixed gap={'0 auto'}>
        <img
          src={ArrowTailDown}
          alt={'ArrowTailDown'}
          style={{ marginLeft: '4px', maxWidth: '16px', height: '20px' }}
        />
      </RowFixed>
      <RowBetween align="flex-end">
        <RowFixed gap={'0px'}>
          <CurrencyLogo currency={trade.outputAmount.currency} size={'24px'} style={{ marginRight: '12px' }} />
          <TruncatedText fontSize={24} fontWeight={500}>
            {trade.outputAmount.currency.symbol}
          </TruncatedText>
        </RowFixed>
        <RowFixed gap={'0px'}>
          <Text
            fontSize={24}
            fontWeight={500}
            style={{ marginLeft: '10px' }}
            color={
              priceImpactSeverity > 2
                ? theme.red1
                : showAcceptChanges && trade.tradeType === TradeType.EXACT_INPUT
                ? theme.orange
                : ''
            }
          >
            {trade.outputAmount.toSignificant(6)}
          </Text>
        </RowFixed>
      </RowBetween>
      {recipient !== null ? (
        <AutoColumn justify="flex-start" gap="sm" style={{ padding: '12px 0 0 0px' }}>
          <TYPE.main>
            Output will be sent to{' '}
            <b title={recipient}>{isAddress(recipient) ? shortenAddress(recipient) : recipient}</b>
          </TYPE.main>
        </AutoColumn>
      ) : null}
    </AutoColumn>
  )
}
